<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div style="height: 100%; width: 100%" align="center">
        <v-progress-circular
                v-if="!ideUrl"
                style="margin-top: 25%; color: #1976d2 !important"
                :size="130"
                color="primary"
                indeterminate
        >
            <a> 30  <br> .</a>
        </v-progress-circular>
        <vue-friendly-iframe
                v-else
                :src="ideUrl"
                style="height: 100%; width: 100%"
                frameborder="0" gesture="media" allow="encrypted-media"></vue-friendly-iframe>
    </div>
</template>

<script>

    import LabBase from "../LabBase"

    export default {
        name: 'lab-tool-ide',
        mixins: [LabBase],
        props: {
            value: Object,
            labInfo: Object,
        },

        data() {
            return {
                ideInterval: '',
                ideUrl: null, //refactor reason #NIM, #VIFWN
                interval: null, //refactor reason #NIM, #VIFWN
                userId: this.$route.params.userId
            }
        },

        created: async function () {

            var me = this;
            me.userId = me.$route.params.userId;

            var hashName = await me.startIDE();
            this.ideInterval = setInterval(function () {
                console.log("ide Interval")
                me.ideRunningCheck(hashName);
                //me.loading = false  // refactor reason #NIM, #VIFWN
            }, 3000)

            //TODO:  API    polling    ..
            me.interval = setInterval(async function () {
                try {
                    var log = await me.getString(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/result.log`)
                    me.onResultChange(log)
                } catch (e) {

                }
            }, 1000)
        },

        mounted: function () {
            console.log(this.labInfo)

        },
        beforeDestroy() {
            clearInterval(this.interval)
        },
        methods: {
            onResultChange(change) {
                this.value.result = change;
                this.$emit('input', this.value);
                this.$emit('change', this.value);
            },
            hashCode(s) {
                return s.split("").reduce(function (a, b) {
                    a = ((a << 5) - a) + b.charCodeAt(0);
                    return a & a
                }, 0);
            },
            ideRunningCheck(hashName) {
                var me = this
                    me.$http.get(`http://34.69.232.192/api/v1/namespaces/default/pods/${hashName}/status`).then(function (result) {
                        if (result.data.status.conditions) {
                            result.data.status.conditions.forEach(function (status) {
                                if (status.type == "Ready" && status.status == "True") {
                                    me.ideUrl = `http://${hashName}.msastudy.io/`;
                                    clearInterval(me.ideInterval);
                                }
                            })
                        }
                    }).catch(function (e) {
                    })
            },
            ideExistCheck(hashName) {
                var me = this
                return new Promise(function (resolve) {
                    me.$http.get(`http://34.69.232.192/api/v1/namespaces/default/pods/${hashName}/status`).then(function (result) {
                        resolve(true)
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
            startIDE() {
                var me = this
                return new Promise(async function (resolve) {
                    // var email = window.localStorage.getItem('email')
                    var course = me.courseId;
                    var clazz = me.classId;
                    var lab = me.labId;
                    var userEmail = me.userId;
                    var filePath = course + '/runningClass/' + clazz + '/labs/' + lab + '/' + userEmail;
                    var hashName = "labs-" + me.hashCode(filePath);
                    var ideExistChecked = await me.ideExistCheck(hashName);
                    var templateFile = me.labInfo.templateFile;
                    if (ideExistChecked) {
                        resolve(hashName)
                    }
                    if (me.labInfo.templateFile) {
                        me.$http.post(`http://34.69.232.192/api/v1/namespaces/default/pods`, {
                            "apiVersion": "v1",
                            "kind": "Pod",
                            "metadata": {
                                "name": `${hashName}`,
                                "labels": {
                                    "environment": "labs",
                                    "app": `${hashName}`
                                }
                            },
                            "spec": {
                                "initContainers": [
                                    {
                                        "name": "mkdir",
                                        "image": "busybox",
                                        "command": ["mkdir", "-p", `/home/project/${filePath}/${templateFile}`],
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": "labs"
                                            }
                                        ]
                                    },
                                    {
                                        "name": "unzip",
                                        "image": "busybox",
                                        "command": ["unzip", "-n", `/home/project/${course}/planed/${lab}/${templateFile}`, "-d", `/home/project/${filePath}/${templateFile}`],
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": "labs"
                                            }
                                        ]
                                    }
                                ],
                                "containers": [
                                    {
                                        "name": `${hashName}`,
                                        "image": "sanghoon01/theia-labs",
                                        "securityContext": {
                                            "privileged": true,
                                            "runAsUser": 0
                                        },
                                        "env": [
                                            {
                                                "name": "COURSE",
                                                "value": `${course}`
                                            },
                                            {
                                                "name": "LABS",
                                                "value": `${lab}`
                                            },
                                            {
                                                "name": "CLAZZ",
                                                "value": `${clazz}`
                                            },
                                            {
                                                "name": "EMAIL",
                                                "value": `${userEmail}`
                                            },

                                        ],
                                        "resources": {
                                            "requests": {
                                                "memory": "2Gi"
                                            }
                                        },
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/var/run/docker.sock",
                                                "name": "dockersock"
                                            },
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": `labs/${filePath}/${templateFile}`
                                            }
                                        ],
                                        "readinessProbe": {
                                            "httpGet": {
                                                "path": "/",
                                                "port": 3000,
                                                "initialDelaySeconds": 10,
                                                "periodSeconds": 5
                                            }
                                        },
                                    }
                                ],
                                "volumes": [
                                    {
                                        "name": "dockersock",
                                        "hostPath": {
                                            "path": "/var/run/docker.sock",
                                            "type": "File"
                                        }
                                    },
                                    {
                                        "name": "test-storage",
                                        "persistentVolumeClaim": {
                                            "claimName": "nfs"
                                        }
                                    }
                                ]
                            }
                        }).then(function () {
                            me.$http.post(`http://34.69.232.192/api/v1/namespaces/default/services`, {
                                "apiVersion": "v1",
                                "kind": "Service",
                                "metadata": {
                                    "labels": {
                                        "environment": "labs",
                                        "app": `${hashName}`
                                    },
                                    "name": `${hashName}`,
                                    "namespace": "default"
                                },
                                "spec": {
                                    "ports": [
                                        {
                                            "port": 3000,
                                            "protocol": "TCP",
                                            "targetPort": 3000
                                        }
                                    ],
                                    "selector": {
                                        "app": `${hashName}`
                                    },
                                    "sessionAffinity": "None",
                                    "type": "ClusterIP"
                                },
                                "status": {
                                    "loadBalancer": {}
                                }
                            }).then(
                                function () {
                                    var metaData = {
                                        email: userEmail,
                                        status: "start",
                                        startTime: new Date(),
                                        result: false
                                    }
                                    me.$minioClient.putObject('labs', `${filePath}/User_Lab_Metadata.json`, JSON.stringify(metaData))
                                    resolve(hashName)
                                }
                            )
                        })
                    } else {
                        me.$http.post(`http://34.69.232.192/api/v1/namespaces/default/pods`, {
                            "apiVersion": "v1",
                            "kind": "Pod",
                            "metadata": {
                                "name": `${hashName}`,
                                "labels": {
                                    "environment": "labs",
                                    "app": `${hashName}`
                                }
                            },
                            "spec": {
                                "initContainers": [
                                    {
                                        "name": "mkdir",
                                        "image": "busybox",
                                        "command": ["mkdir", "-p", `/home/project/${filePath}/${userEmail}`],
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": "labs"
                                            }
                                        ]
                                    }
                                ],
                                "containers": [
                                    {
                                        "name": `${hashName}`,
                                        "image": "sanghoon01/theia-labs",
                                        "securityContext": {
                                            "privileged": true,
                                            "runAsUser": 0
                                        },
                                        "readinessProbe": {
                                            "httpGet": {
                                                "path": "/",
                                                "port": 3000,
                                                "initialDelaySeconds": 10,
                                                "periodSeconds": 5
                                            }
                                        },
                                        "env": [
                                            {
                                                "name": "COURSE",
                                                "value": `${course}`
                                            },
                                            {
                                                "name": "LABS",
                                                "value": `${lab}`
                                            },
                                            {
                                                "name": "CLAZZ",
                                                "value": `${clazz}`
                                            },
                                            {
                                                "name": "EMAIL",
                                                "value": `${userEmail}`
                                            },

                                        ],
                                        "resources": {
                                            "requests": {
                                                "memory": "2Gi"
                                            }
                                        },
                                        "volumeMounts": [
                                            {
                                                "mountPath": "/var/run/docker.sock",
                                                "name": "dockersock"
                                            },
                                            {
                                                "mountPath": "/home/project",
                                                "name": "test-storage",
                                                "subPath": `labs/${filePath}/${userEmail}`
                                            }
                                        ],
                                    }
                                ],
                                "volumes": [
                                    {
                                        "name": "dockersock",
                                        "hostPath": {
                                            "path": "/var/run/docker.sock",
                                            "type": "File"
                                        }
                                    },
                                    {
                                        "name": "test-storage",
                                        "persistentVolumeClaim": {
                                            "claimName": "nfs"
                                        }
                                    }
                                ]
                            }
                        }).then(function () {
                            me.$http.post(`http://34.69.232.192/api/v1/namespaces/default/services`, {
                                "apiVersion": "v1",
                                "kind": "Service",
                                "metadata": {
                                    "labels": {
                                        "environment": "labs",
                                        "app": `${hashName}`
                                    },
                                    "name": `${hashName}`,
                                    "namespace": "default"
                                },
                                "spec": {
                                    "ports": [
                                        {
                                            "port": 3000,
                                            "protocol": "TCP",
                                            "targetPort": 3000
                                        }
                                    ],
                                    "selector": {
                                        "app": `${hashName}`
                                    },
                                    "sessionAffinity": "None",
                                    "type": "ClusterIP"
                                },
                                "status": {
                                    "loadBalancer": {}
                                }
                            }).then(
                                function () {
                                    var metaData = {
                                        email: userEmail,
                                        status: "start",
                                        startTime: new Date(),
                                        result: false
                                    }
                                    me.$minioClient.putObject('labs', `${filePath}/User_Lab_Metadata.json`, JSON.stringify(metaData))
                                    resolve(hashName)
                                }
                            )
                        })
                    }

                })
            }
        },

    }
</script>
<style>
    iframe {
        height: 100%;
        width: 100%;
    }
</style>

