<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!isRead && movingElement"
                :resizable="!isRead && movingElement"
                :deletable="!isRead"

                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"

                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"

                v-on:dblclick="showProperty()"
                v-on:rotateShape="onRotateShape"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="namePanel"
                :image.sync="setImg"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    v-if="movingElement"
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5FC08B',
          fill: '#5FC08B',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#5FC08B',
          fill: '#5FC08B',
          'fill-opacity': .5,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements v-for="(index) in newEditUserImg.length">
                <image-element
                        v-bind:image="newEditUserImg[index-1].picture"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="(10*(index-1))+'px'"
                        :sub-bottom="value.elementView.height"
                >
                </image-element>
            </sub-elements>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< View >>'">
                </text-element>
                <image-element
                        v-if="alertImage "
                        v-bind:image="failed_image"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <!--        <modeling-property-panel-->
        <!--                v-model="value"-->
        <!--                :titleName="'View'"-->
        <!--                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"-->
        <!--        >-->
        <!--        </modeling-property-panel>-->

        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                :titleName="'View'"
                :errorMsg="errorMsg"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/view.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    export default {
        mixins: [Element],
        name: 'view-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.namePanel == '' && Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context and Must has a name.'
                } else if (Object.keys(me.value.boundedContext).length == 0) {
                    return 'Element must be in a Bounded Context.'
                } else if (me.namePanel == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            alertImage() {
                var me = this
                if (me.namePanel == '' && Object.keys(me.value.boundedContext).length == 0) {
                    return true
                } else {
                    return false
                }
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.View'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    aggregate: {},
                    namePascalCase: '',
                    namePlural: '',
                    boundedContext: {},
                    fieldDescriptors: [
                        {
                            _type: "org.uengine.model.FieldDescriptor",
                            name: "id",
                            className: "Long",
                            nameCamelCase: 'id',
                            namePascalCase: 'Id',
                            isKey: true
                        }
                    ],
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    editingView: false,
                    dataProjection: 'cqrs',
                    createRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'CREATE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],
                    updateRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'UPDATE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],
                    deleteRules: [
                        {
                            _type: 'viewStoreRule',
                            operation: 'DELETE',
                            when: {},
                            fieldMapping: [{"viewField": {}, "eventField": {}}],
                            where: [{"viewField": {}, "eventField": {}}],
                        }
                    ],


                }
            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                isValueChanged: false,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$nextTick(function () {
                if (this.namePanel != null) {
                    this.namePanel = me.value.name
                }
                me.isRead = designer.isRead
            })

        },
        watch: {
            "movingElement": function () {
                this.refreshImg()
            },
            "alertImage": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.refreshImg()
                // me.$nextTick(function () {
                //     if (newVal.length != 0) {
                //         this.setImg = newVal[newVal.length - 1].picture
                //     } else {
                //         this.setImg = ''
                //     }
                // })
            },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
            "openPanel": function (newVal) {
                var me = this
                if (newVal == false) {

                    if (me.isValueChanged) {

                        me.isValueChanged = false
                    } else {

                    }

                }

            },
            "value.dataProjection": function (newVal, oldVal) {
                var me = this
                // console.log(newVal)
                if (newVal == 'cqrs') {
                    me.value.checkValue = true
                } else if (newVal == 'uimashup') {
                } else if (newVal == 'graphql') {
                }
            }
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            me.$nextTick(function () {
                me.isRead = designer.isRead
            })

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if (obj.state == 'elementMove') {
                    me.movingElement = true
                    var afterMove = JSON.parse(obj.element)
                    me.value.elementView.x = afterMove.x
                    me.value.elementView.y = afterMove.y
                    me.value.elementView.width = afterMove.width
                    me.value.elementView.height = afterMove.height
                }


                if (obj.state == 'editUserPanelOpen' || obj.state == 'editUserSelectOn') {

                    if (me.newEditUserImg.findIndex(user => user.uid == obj.uid) == -1) {
                        me.newEditUserImg.push(obj)
                    }

                } else if (obj.state == 'editUserPanelClose' || obj.state == 'editUserSelectOff') {
                    if (me.newEditUserImg.length > 0) {
                        me.newEditUserImg.forEach(function (user, idx) {
                            if (user.uid == obj.uid) {
                                me.newEditUserImg[idx] = null
                            }
                        })
                        me.newEditUserImg = me.newEditUserImg.filter(n => n)
                    }
                }

            })


            var tmp = me.value.dataProjection;
            // me.value.dataProjection = '';
            this.$nextTick(function () {
                if (tmp == 'cqrs') {
                    // // console.log("")
                    // console.log(me.value.dataProjection)

                    // me.value.viewHeaders = [
                    //     {
                    //         text: 'Key',
                    //         align: 'left',
                    //         sortable: false,
                    //         value: 'isKey',
                    //     },
                    //     {text: 'columnName', value: 'name'},
                    //     {text: 'type', value: 'className'},
                    //     {text: 'sourceEvent', value: 'sourceEvent'},
                    //     {text: 'sourceEvent or DirectValue', value: 'eventDirectValue'},
                    //     {text: 'viewColumnName', value: 'viewColumnName'},
                    //     {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                    //     {text: 'Actions', value: 'action'},
                    // ]

                    // me.value.viewHeaders = [
                    //     {text: 'viewColumn', value: 'viewColumn'},
                    //     {text: 'viewColumnType', value: 'viewColumnType'},
                    //     {text: 'selectEvent', value: 'selectEvent'},
                    //     {text: 'sourceEventColumn', value: 'sourceEventColumn'},
                    //     {text: 'defaultValue', value: 'defaultValue'},
                    //     {text: 'Actions', value: 'action'}
                    // ]
                    // me.value.viewFieldDescriptors = me.value.fieldDescriptorsCqrs
                } else if (tmp == 'uimashup') {
                    // console.log(me.value.dataProjection)

                    // me.value.viewHeaders = [
                    //     {text: 'type', value: 'className'},
                    // ]
                    //
                    // me.value.viewFieldDescriptors = me.value.fieldDescriptorsMash
                } else if (tmp == 'graphql') {
                    // console.log(me.value.dataProjection)

                }
            })
        },
        methods: {
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },
        }
    }
</script>


