<template>
    <v-card class="elevation-12" style="background: #FFFFFF">

        <v-card flat style="background: #ffffff">
            <v-col justify="center" align="center">
                <v-btn
                        block
                        color="#ffffff"
                        style="width: 250px; font-size: 13px; color: #175FC7; justify-content: center; "
                        @click="snsLogin('google')"
                >
                    <img width="20px"
                         style="margin-bottom:3px; margin-right:15px"
                         alt="Google sign-in"
                         src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"/>
                    Login in with Google
                </v-btn>
                <div class="pa-2"></div>
                <v-btn
                        block
                        color="#000000"
                        style="width: 250px;font-size: 13px; color: white; justify-content: center; "
                        @click="snsLogin('github')">
                    <img width="30px"
                         style="margin-bottom:3px; margin-right:5px;  background-color: white;-webkit-filter: invert(100%);filter: invert(100%);"
                         alt="Github sign-in"
                         src="https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png"/>
                    &nbsp; Login in with Github
                </v-btn>

            </v-col>
        </v-card>

    </v-card>
</template>

<script>

    import firebase from 'firebase'

    require('firebase/auth')

    export default {
        components: {},
        props: {
        },
        data: () => ({
            drawer: null,

        }),
        created(){
        },
        methods: {
            writeUserData(userId, name, email, imageUrl, provider) {
                var database = firebase.database();
                var authorized = 'admin';
                if (email.includes('@uengine.org')) {
                    authorized = 'admin'
                } else {
                    authorized = 'student'
                }
                database.ref('users/' + userId).update({
                    username: name,
                    email: email,
                    profile_picture: imageUrl,
                    state: 'signIn',
                    provider:provider,
                    authorized: authorized
                });
            },
            snsLogin(who) {
                var me = this
                var app = this.getComponent('App')
                var event = this.getComponent('EventStormingListPage')
                if (who == 'google') {
                    var provider = new firebase.auth.GoogleAuthProvider();
                    provider.addScope('profile');
                    provider.addScope('email')

                    firebase.auth().signInWithPopup(provider).then(function (result) {


                        var token = result.credential.accessToken;
                        var uid = result.user.uid;
                        var user = result.additionalUserInfo.profile;
                        var state = result.operationType;

                        window.localStorage.setItem("author", user.email)
                        window.localStorage.setItem("userName", user.name)
                        window.localStorage.setItem("email", user.email)
                        window.localStorage.setItem("picture", user.picture)
                        window.localStorage.setItem("accessToken", token)
                        window.localStorage.setItem("uid", uid)
                        if (user.email.includes('@uengine.org')) {
                            window.localStorage.setItem("authorized", 'admin');
                        } else {
                            window.localStorage.setItem("authorized", 'student');
                        }
                        app.loginDialog = false
                        me.$EventBus.$emit('login', token)

                        //firebase DB input
                        me.writeUserData(uid, user.name, user.email, user.picture, 'google')
                    }).catch(function (error) {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        alert("[" + errorCode + "] " + errorMessage)
                    });


                } else if (who == 'github') {


                    var provider = new firebase.auth.GithubAuthProvider();
                    provider.addScope('repo');
                    provider.addScope('user');

                    firebase.auth().signInWithPopup(provider).then(function (result) {

                        console.log(result)
                        var token = result.credential.accessToken;
                        var uid = result.user.uid;
                        var userName =  result.additionalUserInfo.username
                        var userEmail =result.user.providerData[0].email
                        var userProfile = result.additionalUserInfo.profile.avatar_url;
                        var state = result.operationType;

                        window.localStorage.setItem("author", userEmail)
                        window.localStorage.setItem("userName",userName)
                        window.localStorage.setItem("email", userEmail)
                        window.localStorage.setItem("picture", userProfile)
                        window.localStorage.setItem("accessToken", token)
                        window.localStorage.setItem("gitAccessToken", token)
                        window.localStorage.setItem("uid", uid)

                        app.loginDialog = false
                        me.$EventBus.$emit('login', token)

                        //firebase DB input
                        me.writeUserData(uid, userName, userEmail, userProfile, 'github')

                    }).catch(function (error) {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        alert("[" + errorCode + "] " + errorMessage)

                        // if (error.code == 'auth/account-exists-with-different-credential') {
                        //
                        //     var pendingCred = error.credential;
                        //     var email = error.email;
                        //
                        //     firebase.auth().fetchSignInMethodsForEmail(email).then(function(providers) {
                        //         window.localStorage.setItem("ttttttttt", providers)
                        //         console.log(providers)
                        //
                        //     });
                        //
                        // }
                    });


                } else if (who == 'facebook') {

                }


            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },

        }
    }
</script>

