<template>

</template>

<script>
    var Minio = require('minio');

    export default {
        name: "LabBase",
        components: {},
        props: {
            // courseId: String,
            // classId: String,
            // labId: String, 
        },
        computed: {
            minioClient() {

                return new Minio.Client({
                    endPoint: "35.232.244.51",
                    port: 9000,
                    useSSL: false,
                    accessKey: "minio",
                    secretKey: "minio123"
                })
            },

            myId() {
                return localStorage.getItem("email");
            },

            isAdmin() {
                return (window.localStorage.getItem('authorized') == 'admin')
            },
            courseId() {

                return this.$route.params.courseId;
            },
            labId() {

                return this.$route.params.labId;
            },
            classId() {

                return this.$route.params.classId;
            },

        },
        data: {},
        beforeDestroy() {
        },

        created() {
            var me = this


        },
        methods: {

            getLabInfo(labId) {
                var me = this
                if(!labId) labId = me.labId;

                return new Promise(async function (resolve) {
                    var labPath = `${me.courseId}/planed/${labId}/Lab_Metadata.json`

                    var labInfo = await me.getObject(labPath);
                    var markdown = await me.getString(`${me.courseId}/planed/${me.labId}/instruction.md`)
                    try{
                        labInfo.instructionHtml = marked(markdown, {sanitize: true})
                    }catch(e){
                        labInfo.instructionHtml = e.message
                    }
                    //todo: load javascript here

                    resolve(labInfo);
                })

            },

            async getClassInfo(){
                var path = `${this.courseId}/runningClass/${this.classId}/Class_Metadata.json`

                return await this.getObject(path);
            },

            async getCourseInfo(){
                return await this.getObject(`${this.courseId}/Course_Metadata.json`);
            },

            _list(path){
                var me = this;
                var listObj = [];

                return new Promise((resolve, reject) => {
                    this.$minioClient.listObjects('labs', path, false)
                        .on('data', function (obj) {
                            listObj.push(obj)
                        })
                        .on('error', function (err) {
                            reject(err);
                        })
                        .on('end', function (err) {

                            var list = [];

                            listObj.sort(function (a, b) {
                               return a.lastModified - b.lastModified;
                            })
                        
                            listObj.forEach(obj => {list.push(obj.name)})

                            resolve(list);
                        })
                });
            },

            async _get(path) {
                var me = this

                var exist = await me._list(path);
                if(exist.length==0) return null;

                return new Promise((resolve, reject) => {
                    me.minioClient.getObject('labs', path, function (err, labDataStream) {
                        if (err) {
                            reject(err);
                            return;
                        }
                        if (!labDataStream) {
                            reject(new Error("no stream connected"));
                            return;
                        }
                        labDataStream.on('data', function (labChunk) {
                            var string = new TextDecoder("utf-8").decode(labChunk)
                            resolve(string);
                        })

                        labDataStream.on('error', function(err) {
                            reject(err);
                        })
                    })
                });
            },
            _put(path, string) {
                var me = this;

                return new Promise((resolve, reject) => {
                    me.minioClient.putObject('labs', path, string, function (err, etag) {
                        if (err == null && etag) {
                            resolve(etag);
                        } else
                            reject(err);

                    });
                });
            },
            _delete(path) {
              var me = this
                return new Promise((resolve,reject) => {
                    me.minioClient.removeObject('labs', path, function(err) {
                        if (err) {
                            reject(err)
                        }
                        resolve()
                    })
                })
            },

            async list(path) {
                return await this._list(path);
            },

            async getString(path) {
                return await this._get(path);
            },
            async delete(path) {
                return await this._delete(path)
            },
            async getObject(path) {
                var string = await this.getString(path);
                var data = JSON.parse(string);

                return data;
            },

            async putString(path, string) {
                return await this._put(path, string);
            },
            async putObject(path, obj) {
                var string = JSON.stringify(obj);
                return await this.putString(path, string);
            },

        }
    };
</script>
