<script>
    import axios from 'axios'
    import https from 'https'
    import firebase from 'firebase'
    import CourseNavigator from "./components/labs/CourseNavigator";

    export default {
        name: 'App',
        props: {},
        data: () => ({
            terminalUrl: '',
            terminal: false,
            openGuide: false,
            // iframeLoading: true,
            infoSlider: [
                'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/event.png',
                'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/policy.png',
            ],
            messageLists: [],
            infoNum: 0,
            chatWindow: false,
            dialog: false,
            drawer: false,
            infoDialog: false,
            kubeHost: '',
            kubeToken: '',
            loginDialog: false,
            ideLoading: false,
            items: [
                // {icon: 'fa-book', text: 'Introduce', route: '/'},
                // {icon: 'fa-sticky-note', text: 'EventStormingListPage', route: '/' },
                {icon: 'fa-sticky-note', text: 'EventStorming', route: '/'},
            ],
            api: [],
            snackbar: false,
            fab: false,
            courseNavi: false,
            overlay: true,
            // loginUser: {},
            successLogin: false,
            message: '',
            uid: '',
            userName: '',
            messageRef: {},
            progressValue: false,
            showParticiateLists: false,
            particiateLists: [],
            naviObject: {
                drawer: false,
                instruction: [],
                checkPoints: [],
                hints: [],
                logs: ''
            },
            iFrame: false,
            iFrameUrl: '',
            interval: '',


        }),
        components: {
            axios,
            https,
            CourseNavigator
        },
        beforeDestroy() {
            var me = this
            window.localStorage.removeItem("accessToken");
            // me.messageRef = firebase.database().ref(`/${me.chatUid}/${me.$route.params.projectName}`);

            // window.localStorage.removeItem("projectName");
            // window.localStorage.removeItem("picture");
            // window.localStorage.removeItem("loadData");
        },
        // beforeMount(){
        // },
        computed: {
            activeFab() {
                switch (this.tabs) {
                    case 'one':
                        return {class: 'purple', icon: 'account_circle'}
                    case 'two':
                        return {class: 'red', icon: 'edit'}
                    case 'three':
                        return {class: 'green', icon: 'keyboard_arrow_up'}
                    default:
                        return {}
                }
            },
            loginUser() {
                var us = {
                    'author': window.localStorage.getItem("author"),
                    'userName': window.localStorage.getItem("userName"),
                    'picture': window.localStorage.getItem("picture"),
                    'email': window.localStorage.getItem("email")
                }
                return us
            },
            authorized() {
                var me = this
                if (window.localStorage.getItem("accessToken") == null) {
                    window.authorized = false;
                    return false
                } else if (window.localStorage.getItem("accessToken")) {
                    // this.$http.defaults.headers.common['Authorization'] = `Bearer ${localStorage.accessToken}`;
                    // var us={
                    //     'author': window.localStorage.getItem("author"),
                    //     'userName':window.localStorage.getItem("userName"),
                    //     'picture': window.localStorage.getItem("picture"),
                    //     'email': window.localStorage.getItem("email")
                    // }
                    me.loginUser

                    window.authorized = true;
                    me.loginDialog = false;
                    return true
                }
            },
        },
        created: function () {
            var me = this

            if (me.$route.name == 'IdeLoadingPage') {
                me.ideLoading = true
            }

            //  
            if (me.authorized) {
                me.successLogin = true
                me.overlay = false
            } else {
                me.successLogin = false
            }


        },
        watch: {},
        beforeDestroy() {
        },
        mounted() {
            var me = this


            if (localStorage.getItem('projectName')) {
                me.overlay = false
            }

            //  
            me.$EventBus.$on('login', function (val) {
                if (val) {
                    me.successLogin = true
                } else {
                    me.successLogin = false
                }
                me.authorized
                // me.vm.$forceUpdate();
                me.$forceUpdate();
            })

            // // console.log()

            me.$EventBus.$on('showLoginDialog', function () {
                me.loginDialog = true
                // me.messageLists.push({'.'})
            })
            me.$EventBus.$on('closeChat', function (val) {
                // console.log("chat close", me.messageRef)
                me.messageLists = [];
                me.messageRef.off();
            })

            me.$EventBus.$on('particiateLists', function (val) {
                me.particiateLists = val
            })

            me.$EventBus.$on('progressValue', function (newVal) {
                me.progressValue = newVal
            })
            me.$EventBus.$on('storming-start', function (data) {
                console.log(data)
                me.courseNavi = true;
                me.naviObject.labsName = data.labName
                me.naviObject.drawer = true;
                me.naviObject.checkPoints = data.checkPoints;
                me.naviObject.hints = data.hints;
                me.naviObject.lab = data.lab;
                me.naviObject.instruction = data.instruction;
                me.naviObject.email = data.email

            })
            this.$EventBus.$on('guideClose', function () {
                me.openGuide = true
            })
            // this.$EventBus.$on('ide-open', function (data) {
            //     console.log(data)
            //     me.iFrameUrl = data.ideURL;
            //     me.courseNavi = true;
            //     me.iFrame = true;
            //     me.naviObject.labsName = data.labName
            //     me.naviObject.drawer = true;
            //     me.naviObject.checkPoints = data.checkPoints;
            //     me.naviObject.hints = data.hints;
            //     me.naviObject.instruction = data.instruction;
            //     me.interval = setInterval(function() {
            //         me.getLog(data.email)
            //     },1000)
            // });

            $(document).keydown((evt) => {
                if (evt.keyCode == 27) {
                    this.overlay = false
                }
            });

            if (me.$route.query.access_token) {
                localStorage.setItem("accessToken", me.$route.query.access_token)
                var tmpURL = window.location.protocol + "//" + window.location.host + "/";
                window.location.href = tmpURL;
            }

            var users = me.getUid();
            users.then(function (user) {
                me.userName = user.displayName;
                me.uid = user.uid
            })
        },
        methods: {
            goMessage() {
                var me = this
                me.$router.push('/MessageTest')
            },
            openPanelParticiateLists() {
                this.$EventBus.$emit('participantsListsPanel', true)
            },
            // Multi(){
            //   this.$EventBus.$emit('webrtcDialog')
            // },
            // onLoad() {
            //     console.log('iframe loaded');
            //     this.iframeLoading = false;
            // },
            // onIframeLoad() {
            //     console.log('iframe loaded');
            // },
            async sendMessage() {

                var me = this
                if (me.message.length > 0) {
                    var users = me.getUid();
                    if (me.userName.length < 1 && me.uid.length < 1) {

                        users.then(function (user) {
                            me.userName = user.displayName;
                            me.uid = user.uid

                            me.messageRef = firebase.database().ref(`/${me.chatUid}/${me.$route.params.projectName}`);
                            me.messageRef.push({message: me.message, userName: me.userName, uid: me.uid});
                            me.message = '';
                        })
                    } else {
                        me.messageRef = firebase.database().ref(`/${me.chatUid}/${me.$route.params.projectName}`);
                        me.messageRef.push({message: me.message, userName: me.userName, uid: me.uid});
                        me.message = '';
                    }
                }
            },
            closeIFrame() {
                var me = this
                // me.$EventBus.$emit('clearInterval', me.naviObject.labsName);

                me.$nextTick(function () {
                    me.courseNavi = false
                    me.naviObject = {
                        labsName: '',
                        drawer: false,
                        instruction: [],
                        checkPoints: [],
                        hints: [],
                        logs: ''
                    }
                    me.iFrame = false;
                    me.iFrameUrl = '';
                    me.naviObject.drawer = false;
                    clearInterval(me.interval)
                })
            },
            // getLog(userId) {
            //     var me = this
            //     var stream = this.$minioClient.listObjects('labs', this.$route.params.course + '/runningClass/' + this.$route.params.clazzName + '/' + this.$route.params.labName + '/' + userId + '/', false);
            //     stream.on('data', function (data) {
            //         if (data.name) {
            //             if (data.name.includes('result.log')) {
            //                 me.$minioClient.getObject('labs', data.name, function (err, dataStream) {
            //                     if (err) {
            //                         // alert(err)
            //                     }
            //                     dataStream.on('data', function (chunk) {
            //                         var string = new TextDecoder("utf-8").decode(chunk);
            //                         me.naviObject.logs = string
            //                     })
            //                 })
            //             }
            //         }
            //     })
            // },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user);
                        } else {
                            // No user is signed in.
                            resolve("everyone")
                        }
                    });
                })
            },
            logout() {
                var me = this
                var userId = localStorage.getItem('uid')

                firebase.database().ref('users/' + userId).update({
                    state: 'signOut'
                });

                firebase.auth().signOut().then(function (result) {
                    // Sign-out successful.
                    console.log('logout')

                    window.localStorage.removeItem("gitAccessToken");
                    window.localStorage.removeItem("accessToken");
                    window.localStorage.removeItem("author");
                    window.localStorage.removeItem("userName");
                    window.localStorage.removeItem("email");
                    window.localStorage.removeItem("projectName");
                    window.localStorage.removeItem("picture");
                    window.localStorage.removeItem("loadData");
                    window.localStorage.removeItem("uid");

                    me.$EventBus.$emit('login', localStorage.getItem("accessToken"))

                    window.localStorage.clear()
                    var newURL = window.location.protocol + "//" + window.location.host + "/";
                    window.location.href = newURL;
                }).catch(function (error) {
                    // An error happened.
                });

                // this.$store.commit('LOGOUT')
            },

            saveSetting() {
                var me = this;
                me.dialog = false;
                this.$http.put(`${API_HOST}/kube/user/saveUserDetail`, {
                    username: me.userInfo.user_name,
                    host: me.kubeHost,
                    token: me.kubeToken
                }).then((result) => {
                    let tmp = {kubeHost: me.kubeHost, kubeToken: me.kubeHost, userName: me.userInfo.user_name}
                    me.$store.dispatch('LOGIN', tmp)
                })
            },
            loginPage() {
                var me = this
                me.loginDialog = true;
            },
            wikiOpen() {
                window.open('http://uengine.org/eventstorming/#/')
            },
            guideButtonClose() {
                this.openGuide = false
                this.$EventBus.$emit('guideOpen')
            }

        }
    }
</script>
