<template xmlns:v-on="http://www.w3.org/1999/xhtml">

    <div class="canvas-panel">

        <v-overlay :value="showOverlay">
            <v-col align="center">
                <div>Loading ...</div>
                <v-progress-circular indeterminate size="64">
                    <v-btn text @click="showOverlay = false">close</v-btn>
                </v-progress-circular>
            </v-col>
        </v-overlay>

        <participant-lists-panel v-if="openParticipantsPanel" :lists="participantLists"></participant-lists-panel>

        <v-layout>
            <modal name="uml-modal" :height='"80%"' :width="'80%'">
                <class-modeler></class-modeler>
            </modal>
        </v-layout>

        <v-layout>
            <modal name="bpmn-modal" :height='"80%"' :width="'80%'">
                <bpmn-process-designer></bpmn-process-designer>
            </modal>
        </v-layout>

        <v-snackbar v-model="deleteSnackbar" color="primary" multi-line :timeout="5000">
             BoundedContext ( {{ deleteBounded }} ) .
            <v-btn dark @click="deleteSnackbar = false">
                Close
            </v-btn>
        </v-snackbar>

        <v-snackbar v-model="convertValueSnackbar" color="primary" multi-line :timeout="0">
            <v-progress-circular
                    indeterminate
                    color="green"
            ></v-progress-circular>
            {{ convertValueMessage }}
            <v-btn v-if="isApplyConvertValue" dark @click="convertValueSnackbar = false">
                
            </v-btn>
        </v-snackbar>


        <v-layout right>
            <modal name="code-modal" :width="codeModalWidth" :height='"auto"' scrollable>
                <v-card flat>
                    <v-card-title>
                        <v-col
                                :col="8"
                        >
                            <span class="headline">Code Preview</span>
                        </v-col>
                        <v-col
                                :col="4"
                        >
                            <v-select
                                    :items="templateList"
                                    v-model="template"
                                    label="Select Template"
                                    hide-details
                                    class="pa-0"

                            ></v-select>
                        </v-col>
                    </v-card-title>
                    <v-divider></v-divider>
                    <v-card-text style="width: auto; height: auto;">
                        <v-row
                                class="mb-6"
                                no-gutters
                        >
                            <v-col
                                    style="margin-right: 15px; border-right: 1px solid black; max-height: 700px; max-width: 400px;"
                                    id="scroll-target"
                                    class="overflow-auto"
                            >
                                <v-treeview
                                        :items.sync='treeList'
                                        :active.sync="openCode"
                                        activatable
                                        item-key="key"
                                        return-object
                                        open-all
                                        :transition="treeOpen"
                                        open-on-click
                                        transition
                                        dense
                                >
                                    <template v-slot:prepend="{ item, open }">
                                        <v-icon v-if="!item.file">
                                            {{ open ? 'mdi-folder-open' : 'mdi-folder' }}
                                        </v-icon>
                                        <v-icon v-else>
                                            {{ files[item.file] }}
                                        </v-icon>
                                    </template>
                                </v-treeview>
                            </v-col>
                            <v-col>
                                <code-viewer
                                        v-if="codeViewing"
                                        v-model="openCode"></code-viewer>
                            </v-col>
                            <v-col style="max-height: 250px; margin-top: 12px;" v-if="gitAccessInput">
                                <v-card>
                                    <v-card-title class="headline">Template from Git</v-card-title>
                                    <v-card-text>
                                        <v-col>
                                            <v-text-field
                                                    v-model="gitUrl"
                                                    label="Git Repo URL"
                                            ></v-text-field>
                                            <v-text-field
                                                    disabled-v-if="!gitAccessTokenIs"
                                                    v-model="gitAccessToken"
                                                    label="Github AccessToken"
                                            ></v-text-field>
                                        </v-col>

                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn
                                                color="red darken-1"
                                                text
                                                @click="gitAccessInput = false"
                                        >
                                            Cancel
                                        </v-btn>
                                        <v-btn
                                                color="green darken-1"
                                                text
                                                @click="saveGitToken()"
                                        >
                                            Apply
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-col>
                        </v-row>
                    </v-card-text>
                </v-card>
            </modal>

            <opengraph ref="opengraph" focus-canvas-on-select wheelScalable :labelEditable="true"
                       :dragPageMovable="dragPageMovable" :enableContextmenu="false" :enableRootContextmenu="false"
                       :enableHotkeyCtrlC="false" :enableHotkeyCtrlV="false"
                       :enableHotkeyDelete="false" :enableHotkeyCtrlZ="false" :enableHotkeyCtrlD="false"
                       :enableHotkeyCtrlG="false" :slider="true" :movable="!isRead" :resizable="true" :selectable="true"
                       :connectable="!isRead" v-if="value" v-on:canvasReady="bindEvents" :autoSliderUpdate="true"
                       v-on:connectShape="onConnectShape" :imageBase="imageBase">
                <!---->

                <!--                <div v-for="(element, key ,index) in value['elements']">-->
                <!--                    <component-->
                <!--                            v-if="key != null && element != null && value['elements'][key] != null"-->
                <!--                            :is="getComponentByClassName(element._type)"-->
                <!--                            :value.sync="value['elements'][key]"-->
                <!--                            :ref="key"-->
                <!--                    ></component>-->
                <!--                </div>-->
                <!--                <div v-for="(element, key ,index) in value['relations']">-->
                <!--                    <component-->
                <!--                            v-if="key != null && element != null"-->
                <!--                            :is="getComponentByClassName(element._type)"-->
                <!--                            :value.sync="value['relations'][key]"-->
                <!--                            :ref="key"-->
                <!--                    ></component>-->
                <!--                </div>-->

                <!--                 Array -->
                <div v-for="(element, index) in value.definition">
                    <component
                            v-if="index != null && element != null && element.elementView != undefined"
                            :is="getComponentByClassName(element._type)"
                            :value.sync="value.definition[index]"
                            :ref="element.elementView.id"
                    ></component>
                </div>
                <div v-for="(element, index) in value.relation">
                    <component
                            v-if="element != null"
                            :is="getComponentByClassName(element._type)"
                            :value.sync="value.relation[index]"
                            :ref="element.relationView.id"
                    ></component>
                </div>


            </opengraph>

            <v-layout row>
                <!--                <v-flex v-if="!isRead && (parmType == 'local' || (parmType == 'mine' && (sharedCount == 0 ||sharedCount == -2 ) ))">-->
                <!--                <v-flex v-if=" !((parmType == 'mine' && sharedCount>0) || parmType == 'share') ">-->
                <v-flex v-if=" parmType == 'local' ">
                    <v-row style="margin-left: 80px; margin-top: 10px">
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                                <v-btn text small right @click.native="undo()" v-on="on">
                                    <v-icon medium>mdi-undo</v-icon>
                                </v-btn>
                            </template>
                            <span>Undo</span>
                        </v-tooltip>
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                                <v-btn text small right @click.native="redo()" v-on="on">
                                    <v-icon medium>mdi-redo</v-icon>
                                </v-btn>
                            </template>
                            <span>Redo</span>
                        </v-tooltip>

                    </v-row>
                </v-flex>

                <v-flex style="justify:end; align:start;">
                    <v-row justify="end" align="start" style="margin-right: 10px;">

                        <v-col align="start" justify="end" cols="12" sm="3">
                            <v-text-field
                                    :disabled="parmType == 'every' "
                                    label="Project Name" v-model="projectName"
                                    @click.native="unselectedAll">
                            </v-text-field>
                        </v-col>

                        <!--                        <v-menu-->
                        <!--                                class="pa-2"-->
                        <!--                                open-on-hover-->
                        <!--                                offset-y>-->
                        <!--                            <template v-slot:activator="{ on }">-->
                        <!--                                <v-btn-->
                        <!--                                        style="margin-right: 5px; margin-top: 15px;"-->
                        <!--                                        color="success"-->
                        <!--                                        dark-->
                        <!--                                        @click='testButton()'-->
                        <!--                                        v-on="on"-->
                        <!--                                >-->
                        <!--                                    test-->
                        <!--                                </v-btn>-->
