                        <v-menu
                                v-if="parmType != 'local' && parmType != 'kuberLocal' "
                                class="pa-2"
                                open-on-hover
                                offset-y>
                            <template v-slot:activator="{ on }">

                                <v-btn
                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        @click='goToVersions()'
                                        v-on="on"
                                >
                                    <v-icon>{{files.version}}</v-icon>
                                    Versions
                                </v-btn>
                            </template>
                            <!--                            versionItems-->
                            <v-list>
                                <v-list-item
                                        v-for="(item, index) in versionItems"
                                        :key="index"
                                        @click="functionSelect(item.title,index)"
                                >
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>


                        <v-menu
                                class="pa-2"
                                style="margin-right: 3px"
                                open-on-hover
                                offset-y>
                            <template v-slot:activator="{ on }">
                                <v-btn
                                        style="margin-right: 5px;margin-top: 15px;"
                                        color="orange"
                                        dark
                                        @click="codeModalShow()"
                                        v-on="on"
                                >
                                    <v-icon> {{ files.code}}</v-icon>
                                    CODE
                                </v-btn>
                            </template>
                            <v-list>
                                <v-list-item
                                        v-for="(item, index) in codeItems"
                                        :key="index"
                                        @click="functionSelect(item.title,index)"
                                >
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>

                        <v-menu
                                class="pa-2"
                                offset-y
                                open-on-hover>
                            <template v-slot:activator="{ on }">
                                <v-btn

                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        @click="goToServer()"
                                        v-on="on"
                                >
                                    <v-icon>{{files.open}}</v-icon>
                                    OPEN
                                </v-btn>
                            </template>

                            <v-list>
                                <v-list-item
                                        v-for="(item, index) in openItems"
                                        :key="index"
                                        @click="functionSelect(item.title,index)"
                                >
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item>
                                <div class="text-reader" style="text-align:center; margin-top: 5px">
                                    <label for="ex_file">Open from Local</label>
                                    <input id="ex_file" type="file" @change="loadTextFromFile"/>
                                </div>
                            </v-list>
                        </v-menu>

                        <v-menu
                                v-if="parmUid == 'anonymous'"
                                class="pa-2"
                                open-on-hover
                                offset-y>
                            <template v-slot:activator="{ on }">
                                <v-btn
                                        v-if="!isRead"
                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        @click="$EventBus.$emit('showLoginDialog')"
                                        v-on="on"
                                >
                                    <v-icon>{{files.save}}</v-icon>
                                    SAVE
                                </v-btn>
                            </template>
                            <v-list>

                            </v-list>
                        </v-menu>


                        <v-menu
                                v-if="parmUid != 'anonymous'"
                                class="pa-2"
                                open-on-hover
                                offset-y>
                            <template v-slot:activator="{ on }">
                                <v-btn
                                        v-if="!isRead"
                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        @click="saveDialogReady()"
                                        v-on="on"
                                >
                                    <v-icon>{{files.save}}</v-icon>
                                    SAVE
                                </v-btn>
                                <v-btn
                                        v-else
                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        @click="forkDialogReady()"
                                >
                                    <v-icon>{{files.fork}}</v-icon>
                                    FORK
                                </v-btn>
                            </template>
                            <v-list>
                                <v-list-item
                                        v-for="(item, index) in saveItems"
                                        :key="index"
                                        @click="functionSelect(item.title,index)"
                                >
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                        <v-menu
                                v-if="parmType == 'mine' && !isRead "
                                class="pa-2"
                                offset-y
                                open-on-hover
                        >
                            <template v-slot:activator="{ on }">
                                <v-btn
                                        style="margin-right: 5px; margin-top: 15px;"
                                        color="primary"
                                        dark
                                        v-on="on"
                                        @click="openEventShareDialog()"
                                >
                                    <v-icon>{{files.share}}</v-icon>
                                    SHARE
                                </v-btn>
                            </template>
                            <v-list>
                                <v-list-item
                                        v-for="(item, index) in shareItems"
                                        :key="index"
                                        @click="functionSelect(item.title,index)"
                                >
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                        <!--                        <v-menu-->
                        <!--                                class="pa-2"-->
                        <!--                                offset-y>-->
                        <!--                            <template v-slot:activator="{ on }">-->
                        <!--                                <v-btn-->
                        <!--                                        style="margin-right: 5px; margin-top: 15px;"-->
                        <!--                                        color="primary"-->
                        <!--                                        dark-->
                        <!--                                        @click="terminal()"-->
                        <!--                                        v-on="on"-->
                        <!--                                >-->
                        <!--                                    <v-icon>mdi-powershell</v-icon>-->
                        <!--                                    Deploy-->
                        <!--                                </v-btn>-->

                        <!--                            </template>-->
                        <!--                        </v-menu>-->
                    </v-row>

                </v-flex>


                <v-fab-transition>
                    <v-btn
                            v-if="!rtcLogin && ((parmType == 'mine' && sharedCount > 0) || parmType == 'share')"
                            color="blue"
                            dark
                            fab
                            medium
                            absolute
                            bottom
                            right
                            style="margin-right: 80px; margin-bottom: 47px"
                            @click="onJoin()"
                    >
                        <v-icon>mdi-camera</v-icon>
                    </v-btn>
                </v-fab-transition>
            </v-layout>


            <template>
                <div>
                    <v-row
                            justify="center"
                    >
                        <v-dialog
                                v-model="showVersionsDialog"
                                fullscreen
                                hide-overlay
                                scrollable
                        >
                            <v-card tile>
                                <v-toolbar
                                        class="canvas-panel"
                                        flat
                                        dark
                                        color="primary"
                                >
                                    <v-toolbar-title>Versions</v-toolbar-title>
                                    <v-spacer></v-spacer>
                                    <v-toolbar-items>
                                        <v-btn
                                                icon
                                                dark
                                                @click="showVersionsDialog = false"
                                        >
                                            <v-icon>mdi-close</v-icon>
                                        </v-btn>
                                    </v-toolbar-items>
                                </v-toolbar>

                                <v-list
                                        three-line
                                        subheader
                                >
                                    <v-subheader>Version</v-subheader>
                                    <v-list-item>
                                        <v-list-item-content>
                                            <ViewEventStormingVersionPage></ViewEventStormingVersionPage>
                                        </v-list-item-content>
                                    </v-list-item>
                                </v-list>

                                <div style="flex: 1 1 auto;"></div>
                            </v-card>

                        </v-dialog>
                    </v-row>
                </div>
            </template>


            <v-card class="tools" style="top:100px; text-align: center;">
                <span class="bpmn-icon-hand-tool" v-bind:class="{ icons : !dragPageMovable, hands : dragPageMovable }"
                      _width="30"
                      _height="30" v-on:click="toggleGrip">
                     <v-tooltip md-direction="right">Hands</v-tooltip>
                </span>
                <v-tooltip v-if="!isRead" right v-for="(item, key) in elementTypes" :key="key">
                    <template v-slot:activator="{ on }">
                        <span
                                class="icons draggable"
                                align="center"
                                :_component="item.component"
                                :_width="item.width"
                                :_height="item.height">
                        <img height="30px" width="30px" :src="item.src" v-on="on">
                        </span>
                    </template>
                    <span>{{item.label}}</span>
                </v-tooltip>
            </v-card>
        </v-layout>

        <v-snackbar v-model="snackbar" :color="color" :multi-line="mode === 'multi-line'" :timeout="timeout"
                    :vertical="mode === 'vertical'">
            {{ text }}
            <v-btn dark @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
        <v-dialog v-model="ideCheckDialog" max-width="340">
            <v-card>
                <v-card-title class="headline">Project IDE</v-card-title>
                <v-card-text>
                    <div style="color: #8b0000; font-size: medium">  ?</div>
                    <div style="color: #000000">  ,    .</div>
                    <v-checkbox label=" ,   ." v-model="ideUpdateCheck"></v-checkbox>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="openProjectIDE()">OK</v-btn>
                    <v-btn color="red darken-1" text @click="ideCheckDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="generateZipDialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Generate Zip Archive</v-card-title>
                <v-card-text>
                    <v-select
                            :items="templateList"
                            v-model="template"
                            label="Select Template"
                            hide-details
                            class="pa-0"
                    ></v-select>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="generateZip()">Download</v-btn>
                    <v-btn color="red darken-1" text @click="generateZipDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="tokenDialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Token Dialog</v-card-title>
                <v-card-text>
                    <v-text-field v-model="clusterAddress" label="Cluster Address"
                                  required></v-text-field>
                    <v-text-field v-model="kubernetesToken" label="Kubernetes Token"
                                  required></v-text-field>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="tokenSave()">Download</v-btn>
                    <v-btn color="red darken-1" text @click="tokenDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="saveDialogOpen" max-width="350">
            <v-card>
                <v-card-title class="headline">Save new version</v-card-title>
                <v-card-text>
                    <div v-if="duplicateprojectName">
                        <v-icon small color="red">info</v-icon>
                        There is a duplicate projectName
                    </div>
                    <v-text-field
                            v-model="projectName"
                            label="projectName"
                    ></v-text-field>
                    <v-textarea
                            outline
                            name="input-7-4"
                            label="comment"
                            :counter="255"
                            v-model="saveProjectComment"
                    ></v-textarea>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="saveProjectComplete()">Save</v-btn>
                    <v-btn color="red darken-1" text @click="saveDialogClose()">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="forkDialogOpen" max-width="350">
            <v-card>
                <v-card-title class="headline">Fork</v-card-title>
                <v-card-text>
                    <div v-if="duplicateprojectName">
                        <v-icon small color="red">info</v-icon>
                        There is a duplicate projectName
                    </div>
                    <v-text-field
                            disabled
                            v-model="projectName"
                            label="Origin"
                    ></v-text-field>
                    <v-text-field
                            v-model="forkProjectName"
                            label="projectName"
                    ></v-text-field>
                    <v-textarea
                            outline
                            name="input-7-4"
                            label="comment"
                            :counter="255"
                            v-model="forkProjectComment"
                    ></v-textarea>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="forkProjectComplete()">Fork</v-btn>
                    <v-btn color="red darken-1" text @click="forkDialogClose()">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <v-dialog v-model="eventShareDialog" max-width="600">
            <v-card>
                <v-card-title class="headline">share setting</v-card-title>
                <v-card-text>
                    Authorized user
                    <v-simple-table dense height="150px">
                        <template v-slot:default>
                            <thead>
                            <tr>
                                <td></td>

                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="item in invitationLists">
                                <td>
                                    <!-- user Profile   -->
                                    <v-avatar v-if="item.userPic != ''" size="36">
                                        <img
                                                :src="item.userPic"
                                        >
                                    </v-avatar>
                                    <!-- user    -->
                                    <v-avatar v-else size="36">
                                        <v-icon>mdi-account-circle</v-icon>
                                    </v-avatar>
                                </td>

                                <td class="text-center">
                                    <v-col align="start">
                                        <div> {{item.userName}}</div>
                                        <div>{{ item.email }}</div>
                                    </v-col>
                                </td>
                                <td class="text-center">
                                    <v-tooltip bottom>
                                        <template v-slot:activator="{ on }">
                                            <v-btn v-if="item.write"
                                                   :disabled="item.userName == 'Everyone'"
                                                   text
                                                   v-on="on"
