                if (me.undoRedoIndex == me.undoRedoLastIndex) {
                    if (me.undoRedoArray.length == 0) {
                        // me.undoRedoArray.push(JSON.stringify(me.undoRedoInitValue))
                        me.undoRedoArray.push(JSON.stringify(json))
                        me.undoRedoIndex = me.undoRedoIndex + 1
                        me.undoRedoLastIndex = me.undoRedoIndex
                    } else if (me.undoRedoArray.length < me.undoRedoStorage) {
                        me.undoRedoArray.push(JSON.stringify(json))
                        me.undoRedoIndex = me.undoRedoIndex + 1
                        me.undoRedoLastIndex = me.undoRedoIndex
                    } else {
                        me.undoRedoArray.splice(0, 1)
                        me.undoRedoArray.push(JSON.stringify(json))
                    }
                } else {
                    console.log(" PUSH")
                    me.undoRedoArray.splice(me.undoRedoIndex, me.undoRedoArray.length - me.undoRedoIndex)
                    if (me.undoRedoArray.length < me.undoRedoStorage) {
                        me.undoRedoIndex = me.undoRedoIndex + 1
