            // queueStorageStandard ->snapshotFrequency
            // snapshotValue-> checkLeaderExist
            // queuCount-> receivedQueueItemCountAfterLastSnapshot
            async checkLeaderExist(child) {
                var me = this

                me.database.ref('definitions/' + me.parmProjectId + '/snapshotLists')
                    .orderByKey()
                    .limitToLast(1)
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {
                            var lastSnapshotKey = snapshot.val()[Object.keys(snapshot.val())[0]].lastSnapshotKey
                            console.log('lastSnapshotKey', lastSnapshotKey, 'child.childKey', child.childKey, lastSnapshotKey < child.childKey)

                            if (lastSnapshotKey < child.childKey) {
                                /* new Key  lastSnapshotKey  .  */
                                me.database
                                    .ref('definitions/' + me.parmProjectId + '/snapshotLists/')
                                    .push({
                                        snapshot: JSON.stringify(me.value),
                                        timeStamp: new Date().getTime(),
                                        lastSnapshotKey: child.childKey,
                                    })

                                me.receivedQueueItemCountAfterLastSnapshot = 0

                            } else {
                                me.receivedQueueItemCountAfterLastSnapshot = 0
                            }

                        } else {
                            // init  .
                            me.database
                                .ref('definitions/' + me.parmProjectId + '/snapshotLists/')
                                .push({
                                    snapshot: JSON.stringify(me.value),
                                    timeStamp: new Date().getTime(),
                                    lastSnapshotKey: child.childKey,
                                })
                            me.receivedQueueItemCountAfterLastSnapshot = 0
                        }
                    })


