            //OBJECT-MOVE
            convertArrayToObject(value) {
                var me = this

                if (Object.keys(value).findIndex(key => key == 'definition') != -1) {
                    console.log('convertArrayToObject:Array Value')

                    if (Array.isArray(value.definition)) {
                        me.convertValueSnackbar = true

                        if (value.definition.length > 0) {
                            value.definition.forEach(function (element) {
                                me.$set(me.value['elements'], element.elementView.id, element)
                            })
                        }

                        if (value.relation.length > 0) {
                            value.relation.forEach(function (relation) {
                                me.$set(me.value['relations'], relation.relationView.id, relation)
                            })
                        }
                        me.isApplyConvertValue = true
                    }

                } else {
                    console.log('convertArrayToObject:Object Value')
                    me.value = value
                }

            },
            convertObjectToArray(value) {
                var me = this

                if (Object.keys(value).findIndex(key => key == 'elements') == -1) {
                    console.log('convertObjectToArray: Array Value')

                    me.value = value

                } else {


                    if (!Array.isArray(value['elements'])) {
                        me.convertValueSnackbar = true

                        if (Object.keys(value['elements']).length > 0) {
                            Object.keys(value['elements']).forEach(function (key) {
                                me.value.definition.push(value['elements'][key])
                            })
                        }

                        if (Object.keys(value['relations']).length > 0) {
                            Object.keys(value['relations']).forEach(function (key) {
                                me.value.relation.push(value['relations'][key])
                            })
                        }

                        me.isApplyConvertValue = true
                    }
                }

            },
            sort(lastChild, lastChildIndex) {
                var me = this

                if (me.sortScheduleId) return;

                try {

                    if (lastChild == null) {
                        lastChildIndex = queueFifo.elements.length - 1;
                        lastChild = queueFifo.elements[lastChildIndex];
                        // lastChild.childValue.last = true;
                    }

                    var timePassed = new Date().getTime() - lastChild.childValue.receivedTime;
                    if (timePassed < 200) {

                        if (!me.sortScheduleId)
                            me.sortScheduleId =
                                setTimeout(function () {
                                        me.sortScheduleId = null
                                        me.sort(lastChild, lastChildIndex);
                                    },
                                    300
                                );
                    } else {
                        /* receivedQeue Start */
                        me.$EventBus.$emit('progressValue', true)

                        var countOrdered = lastChildIndex + 1;

                        queueFifo.elements.sort(function (a, b) { // 
                            return a.childKey < b.childKey ? -1 : a.childKey > b.childKey ? 1 : 0;
                        });

                        for (var i = 0; i < countOrdered; i++) {
                            me.isReceiveQueue = true
                            me.receivceQueueDrawElement(queueFifo.elements[i])
                        }

                        for (var i = 0; i < countOrdered; i++) {

                            var firstQueue = queueFifo.dequeue()
                            me.receivedQueueItemCountAfterLastSnapshot = me.receivedQueueItemCountAfterLastSnapshot + 1
                            console.log('queue::', me.receivedQueueItemCountAfterLastSnapshot, '/', me.snapshotFrequency, 'receivedCount', me.receivedCount)

                            if (firstQueue.childValue.editUid == localStorage.getItem('uid') &&
                                me.receivedQueueItemCountAfterLastSnapshot >= me.snapshotFrequency &&
                                me.receivedQueueItemCountAfterLastSnapshot % 10 == 0) {
                                me.checkLeaderExist(firstQueue)
                            }

                        }

                        if (queueFifo.isEmpty()) {
                            /* receivedQeue End */
                            me.$EventBus.$emit('progressValue', false)
                            me.$nextTick(function () {
                                me.initLoad = true
                                if (me.checkOverlay) {
                                    me.$EventBus.$emit('showOverlay', false)
                                }
                            })
                        }

                        lastChildIndex = queueFifo.elements.length - 1;
                        lastChild = queueFifo.elements[lastChildIndex];
                        // lastChild.childValue.last = true;

                        if (lastChildIndex > -1)
                            me.sortScheduleId =
                                setTimeout(function () {
                                        me.sortScheduleId = null
                                        me.sort(lastChild, lastChildIndex);
                                    },
                                    300
                                );

                    }


                } catch (e) {
                    console.log('sortError: ', e, 'lastChild:', lastChild)
                    alert('Concurrent editing is unsynchronized. Page will be reloaded.(Sort)')
                    window.location.reload(true)
                }

            },

