            receivceQueueDrawElement(child) {
                var me = this
                me.isReceiveQueue = true

                if (child != undefined) {
                    if (me.receivedLastKey == child.childKey) {
                        me.isLastQueueKey = true
                    } else {
                        me.isLastQueueKey = false
                    }


                    //origin
                    if (child.childValue.state == 'elementPush') {

                        if (child.childKey < me.prevKey) {
                            console.log('Wrong Order', 'now', child.childKey, 'prev:', me.prevKey, child)
                            alert('Concurrent editing is unsynchronized. Page will be reloaded.')
                            window.location.reload(true)
                            // me.synchronizeValue()
                        }

                        if (child.childKey != me.prevKey) {
                            me.prevKey = child.childKey
                            var element = JSON.parse(child.childValue.item)
                            me.value.definition.push(element)
                        } else {
                            console.log('reduplication Element Push')
                        }

                    } else if (child.childValue.state == 'elementDelete') {
                        var item = JSON.parse(child.childValue.item)

                        var obj = {
                            state: 'delete',
                            element: child.childValue.item
                        }
                        me.$nextTick(function () {
                            me.$EventBus.$emit(`${item.elementView.id}`, obj)
                            me.isReceiveQueue = true
                        })
                    } else if (child.childValue.state == 'elementMove') {

                        var afterMove = child.childValue.after
                        var obj = {
                            state: 'elementMove',
                            element: afterMove,
                        }
                        me.$nextTick(function () {
                            me.$EventBus.$emit(`${child.childValue.elementId}`, obj)
                            me.isReceiveQueue = true
                        })


                    } else if (child.childValue.state == 'valueModify') {

                        try {
                            //isLastQueueKey    patch    ,       .
                            if (me.isLastQueueKey == true && me.initLoad) {
                                if (child.childValue.editUid == localStorage.getItem('uid')) {
                                    //self Edit
                                    me.isReceiveQueue = false
                                } else {
                                    me.isReceiveQueue = true
                                    jsondiffpatch.patch(me.value, JSON.parse(child.childValue.item))
                                }
                            } else {
                                me.isReceiveQueue = true
                                jsondiffpatch.patch(me.value, JSON.parse(child.childValue.item))
                            }
                        } catch (e) {
                            console.log('patchError:', e, JSON.parse(JSON.stringify(me.value)), JSON.parse(child.childValue.item))
                            alert('Concurrent editing is unsynchronized. Page will be reloaded(patchError)')
                            window.location.reload(true)
                            // me.synchronizeValue()

                        }

                    } else if (child.childValue.state == 'relationPush') {

                        if (child.childKey < me.prevKey) {
                            console.log('Wrong Order', 'now', child.childKey, 'prev:', me.prevKey, child)
                            alert('Concurrent editing is unsynchronized. Page will be reloaded.')
                            window.location.reload(true)
                            // me.synchronizeValue()
                        }

                        if (child.childKey != me.prevKey) {
                            me.prevKey = child.childKey
                            var relation = JSON.parse(child.childValue.item)

                            me.value.relation.push(relation);
                        } else {
                            console.log('reduplication Relation Push')
                        }

                    } else if (child.childValue.state == 'relationDelete') {
                        var item = JSON.parse(child.childValue.item)

                        var obj = {
                            state: 'delete',
                            element: child.childValue.item
                        }

                        me.$nextTick(function () {
                            me.isReceiveQueue = true
                            me.$EventBus.$emit(`${item.relationView.id}`, obj)
                        })
                    } else if (child.childValue.state == 'relationMove') {
                        try {
                            var afterMove = child.childValue.after

                            var obj = {
                                state: 'relationMove',
                                element: afterMove
                            }
                            me.$nextTick(function () {
                                me.$EventBus.$emit(`${child.childValue.relationId}`, obj)
                                me.isReceiveQueue = true
                            })

                        } catch (e) {
                            console.log('Wrong Order', 'now', child.childKey, 'prev:', me.prevKey, child)
                            alert('Concurrent editing is unsynchronized. Page will be reloaded.')
                            window.location.reload(true)
                        }

                    }

                    console.log('draw:', child.childKey, child.childValue.timeStamp)
                } else {
                    // alert('Concurrent editing is unsynchronized. Page will be reloaded.')
                    // window.location.reload(true)
                }


            },

            updatePermission() {
                var me = this
                var app = me.getComponent('App')
                if (me.parmType == 'share' || (me.parmType == 'mine' && me.sharedCount > 0)) {
                    me.receivedPermission = me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions/permissions')
                        .on('value', function (value) {
                            if (value.exists()) {

                                var uidIndex = Object.keys(value.val()).findIndex(uid => uid == localStorage.getItem('uid'))

                                if (uidIndex == -1) {
                                    me.isRead = true
                                    app.showParticiateLists = false
                                    me.$router.push('/')
                                    alert('Permission denied')

                                } else {
                                    me.isRead = !(value.val()[Object.keys(value.val())[uidIndex]].write)
                                }
                            }

                        })
                }

            },
            setPermission() {
                var me = this
                var app = me.getComponent('App')

                return new Promise(function (resolve) {
                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/specVersion')
                        .once("value", function (snapshot) {
                            if (snapshot.exists()) {
                                me.specVersion = snapshot.val()
                            }
                        })


                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {

                                me.projectAuthor = snapshot.val()['author']
                                me.projectAuthorEmail = snapshot.val()['authorEmail']
                                me.isExistAuthor = snapshot.val()['isExistAuthor']


                                if (snapshot.val()['type'] == undefined) {
                                    me.database.ref('definitions/' + me.parmProjectId + '/shareOptions').update({
                                        type: 'es',
                                    })
                                    console.log('set Type:es')
                                }


                                if (me.projectAuthor == localStorage.getItem('uid') && me.projectAuthor == me.parmUid) {
                                    //My
                                    me.isRead = false

                                    if (snapshot.val()['permissions'] == undefined) {
                                        me.sharedCount = 0
                                        me.isMineProject = true
                                    } else {

                                        if (Object.keys(snapshot.val()['permissions']).indexOf('everyone') == -1) {
                                            me.sharedCount = Object.keys(snapshot.val().permissions).length

                                        } else {
                                            // me.sharedCount = Object.keys(snapshot.val().permissions).length-1
                                            me.isPublicProject = true
                                        }


                                    }
                                    console.log('Permission setting Complete')
                                } else {
                                    //other Person
                                    if (snapshot.val()['permissions'] == undefined) {

                                        me.isRead = true
                                        app.showParticiateLists = false
                                        alert('Wrong Way')
                                        me.$router.push('/')

                                    } else {
                                        if (Object.keys(snapshot.val()['permissions']).indexOf('everyone') != -1) {
                                            //everyone
                                            me.isPublicProject = true
                                            me.isRead = true
                                            console.log('Permission setting Complete')
                                        } else {
                                            me.sharedCount = Object.keys(snapshot.val().permissions).length

                                            if ((Object.keys(snapshot.val()['permissions']).indexOf(localStorage.getItem('uid')) != -1)) {

                                                if (snapshot.val()['permissions'][localStorage.getItem('uid')].write == false) {
                                                    me.isRead = true
                                                    me.forkDialog = true
                                                } else {
                                                    me.isRead = false
                                                }


                                            } else {
                                                //not permission
                                                me.isRead = true
                                                app.showParticiateLists = false
                                                me.$router.push('/')
                                                alert('Permission denied')
                                            }


                                            console.log('Permission setting Complete')
                                        }

                                    }
                                }
                                me.isSetPermission = true
                                resolve()
                            }
                        })

                })

            },
