            onReceiveQueue() {
                var me = this
                return new Promise(function (resolve, reject) {
                    var nowTime = new Date().getTime()

                    me.database
                        .ref('definitions/' + me.parmProjectId + '/snapshotLists')
                        // .orderByChild('/timeStamp')
                        // .endAt(nowTime)
                        .limitToLast(1)
                        .once('value', function (value) {
                            if (value.exists()) {
                                me.isReceiveQueue = true
                                console.log('setSnapshot:', Object.keys(value.val())[0], 'snapshotLastKey:', Object.values(value.val())[0].lastSnapshotKey)
                                me.convertObjectToArray(JSON.parse(value.val()[Object.keys(value.val())[0]].snapshot))
                                // console.log('getSnapshot::', Object.keys(value.val())[0], 'timeStamp:', value.val()[Object.keys(value.val())[0]].timeStamp)
                            }
                        }).then(function (lastSnapshot) {
                        var snapshotLastKey

                        if (lastSnapshot.exists()) {
                            snapshotLastKey = lastSnapshot.val()[Object.keys(lastSnapshot.val())[0]].lastSnapshotKey
                        } else {
                            snapshotLastKey = ''
                        }

                        me.database
                            .ref('definitions/' + me.parmProjectId + '/queue')
                            .orderByKey()
                            .startAt(snapshotLastKey)
                            .on('child_added', child_added => {

                                if (child_added.exists()) {
                                    // console.log('Key:', child_added.key, 'connected_:', child_added.ref.repo.server_.connected_)
                                    if (me.specVersion == '2.0') {

                                        if (child_added.val().state == 'userEntrance') {

                                            var obj = {
                                                uid: child_added.val().editUid,
                                                email: child_added.val().email,
                                                userName: child_added.val().userName,
                                                picture: child_added.val().picture
                                            }
                                            if (me.participantLists.findIndex(user => user.uid == obj.uid) == -1) {
                                                me.participantLists.push(obj)
                                            }


                                        } else if (child_added.val().state == 'userExit') {
                                            var obj = {
                                                uid: child_added.val().editUid,
                                                email: child_added.val().email,
                                                userName: child_added.val().userName,
                                            }
                                            if (me.participantLists.findIndex(user => user.uid == obj.uid) != -1) {
                                                me.participantLists.splice(me.participantLists.findIndex(user => user.uid == obj.uid), 1)
                                            }

                                        } else if (child_added.val().state == 'editUserPanelOpen' ||
                                            child_added.val().state == 'editUserPanelClose' ||
                                            child_added.val().state == 'editUserSelectOn' ||
                                            child_added.val().state == 'editUserSelectOff') {

                                            var obj = {
                                                state: child_added.val().state,
                                                uid: child_added.val().uid,
                                                picture: child_added.val().picture,
                                            }

                                            me.$EventBus.$emit(`${child_added.val().editElement}`, obj)


                                        } else {
                                            if (!me.receivedQueueExistValue) {
                                                me.receivedQueueExistValue = true
                                            }
                                            me.receivedCount++

                                            var obj = {
                                                _ordered: false,
                                                childKey: child_added.key,
                                                childValue: child_added.val()
                                            }
                                            obj.childValue.key = child_added.key;
                                            obj.childValue.receivedTime = new Date().getTime();

                                            me.receivedLastKey = obj.childKey


                                            queueFifo.enqueue(obj)
                                            me.sort();


                                        }
                                    }
                                    resolve()
                                }
                            })
                    })
                })


            },
