            undo: function () {
                var me = this

                if (!me.drawer) {


                    if (me.parmType == 'local' || (me.parmType == 'mine' && (me.sharedCount == 0 || me.isPublicProject))) {
                        // if (me.parmType == 'local') {

                        var undoElement
                        if (me.undoRedoArray.length > 0) {
                            me.undoRedoIndex = me.undoRedoIndex - 1

                            if (me.undoRedoIndex >= 0) {
                                if (me.undoRedoArray[me.undoRedoIndex] == undefined) {
                                    return;
                                }
                                undoElement = JSON.parse(me.undoRedoArray[me.undoRedoIndex])
                                me.isUndoRedo = true
                            } else if (me.undoRedoIndex < 0) {
                                me.undoRedoIndex = 0
                                me.isUndoRedo = true
                                return true
                            }

                            console.log('>>>> undo <<<<<', undoElement)
                            jsondiffpatch.patch(me.value, jsondiffpatch.reverse(undoElement));

                            me.$EventBus.$emit('storage')
                        }


                    } else {
                        //queue 

                        me.isUndoRedo = true

                        if (me.parmType == 'share' || (me.parmType == 'mine' && me.sharedCount > 0)) {
                            //  undoRedo
                            console.log('get Queue Undo - multi')

                            var getUndoRedoKey = localStorage.getItem('nowUndoRedoKey')
                            // me.undoBeforeQueue(getUndoRedoKey)
                        } else if ((me.parmType == 'mine' && (me.sharedCount == 0 || me.isPublicProject))) {
                            //  undoRedo - mine
                            console.log('get Queue Undo - NO multi')

                            var getUndoRedoKey = localStorage.getItem('nowUndoRedoKey')

                        }

                    }

                }
            },
