            fileType(file) {

                var type;

                if (file.includes('.java')) {
                    type = 'java'
                } else if (file.includes('Dockerfile')) {
                    type = 'docker'
                } else if (file.includes('.xml')) {
                    type = 'xml'
                } else if (file.includes('.yaml') || file.includes('.yml') || file.includes('.properties') || file.includes('mvnw') || file.includes('.groovy')) {
                    type = 'txt'
                } else if (file.includes('md')) {
                    type = 'md'
                } else if (file.includes('.jpg') || file.includes('.png') || file.includes('.jpeg')) {
                    type = 'png'
                } else if (file.includes('.json')) {
                    type = 'json'
                } else if (file.includes('.py')) {
                    type = 'python'
                }

                return type
            }
            ,
            findFile(key, name) {
                var me = this
                var ob = {}

                me.getCodeList.some(function (meta) {
                    if (meta.key == key && meta.fileName == name) {
                        ob = {
                            key: meta.key,
                            code: meta.code,
                            file: meta.file,
                        }
                    }
                })
                if (ob.key == undefined) {
                    ob = {
                        key: name,
                    }
                }
                return ob
            }
            ,
            //
            async codeModalShow() {
                var me = this
                me.getCodeList = []
                me.model = []
                if (me.projectName.length < 1) {
                    me.projectName = window.prompt("Please input your ProjectName")
                }

                me.$EventBus.$emit('showOverlay', true)
                await me.getListSetting()

                me.$EventBus.$emit('showOverlay', false)
                me.gitAccessToken = localStorage.getItem('gitAccessToken')

                if (me.checkName)
                    me.$modal.show('code-modal');
                me.codeViewing = true;
                me.openCode = [];

                me.checkName = true

            }
            ,
            umlModalShow() {
                this.$modal.show('uml-modal');
            }
            ,
            async uploadMinio() {
                var me = this
                if (me.treeList.length > 0) {
                    me.treeList.forEach(function (list) {
                        if (!list.file) {
                            me.reverse(list.children, list.name)
                        } else {
                            me.pathTmp.push({path: list.name, code: list.code})
                        }
                    })

                    var zip = new JSZip();

                    var parents = [];

                    me.pathTmp.forEach(function (generateData) {
                        // if(typeof generateData == String) {
                        //     // zip.file(generateData.path,)
                        // }
                        if (generateData.path.includes('/')) {
                            parents.push(generateData.path.split('/')[0])
                        }
                        zip.file(generateData.path, generateData.code)
                    })

                    parents.forEach(function (prefix) {
                        zip.folder(prefix).forEach(function (relativePath, file) {
                        });
                    })

                    zip.generateAsync({type: "blob"})
                        .then(function (content) {
                            // Force down of the Zip file
                            saveAs(content, `${me.projectName}.zip`);
                        });

                } else {
                    await me.getListSetting();
                    me.generateZip();
                }
            },
            async generateZip() {
                var me = this

                if (me.treeList.length > 0) {
                    me.treeList.forEach(function (list) {
                        if (!list.file) {
                            me.reverse(list.children, list.name)
                        } else {
                            me.pathTmp.push({path: list.name, code: list.code})
                        }
                    })

                    var zip = new JSZip();

                    var parents = [];

                    me.pathTmp.forEach(function (generateData) {
                        // if(typeof generateData == String) {
                        //     // zip.file(generateData.path,)
                        // }
                        if (generateData.path.includes('/')) {
                            parents.push(generateData.path.split('/')[0])
                        }
                        zip.file(generateData.path, generateData.code)
                    })

                    parents.forEach(function (prefix) {
                        zip.folder(prefix).forEach(function (relativePath, file) {
                        });
                    })

                    zip.generateAsync({type: "blob"})
                        .then(function (content) {
                            // Force down of the Zip file
                            saveAs(content, `${me.projectName}.zip`);
                        });

                } else {
                    await me.getListSetting();
                    me.generateZip();
                }
