            fileExistCheck(minioClient, metaData) {
                var me = this
                var userName = localStorage.getItem("author").split('@')[0].toLowerCase();
                var userGroup = localStorage.getItem("author").split('@')[1].split('.')[0].toLowerCase();
                var projectName = me.projectName.toLowerCase();
                var fileExist = false
                if (metaData.type == 'project') {
                    return new Promise(function (resolve) {
                        minioClient.statObject('eventstorming', `${userGroup}/${userName}/${projectName}.zip`, function (err, stat) {
                            if (err) {
                                minioClient.statObject('eventstorming', `${userGroup}/${userName}/${projectName}.check`, function (errCheckFile, statCheckFile) {
                                    if (statCheckFile == undefined) {
                                        resolve(false)
                                    } else {
                                        resolve("rebuild")
                                    }
                                })

                            } else {
                                resolve(true)
                            }
                        })
                    })
                } else if (metaData.type == "bounded") {
                    return new Promise(function (resolve) {
                        minioClient.statObject('eventstorming', `${userGroup}/${userName}/${projectName}/${metaData.boundedContext}.zip`, function (err, stat) {
                            if (err) {
                                resolve(false)
                            }
                            console.log(stat)
                            resolve(true)
                        })
                    })
                }
            },
            getPodStatus(userName, userGroup, projectName) {
                var me = this
                return new Promise(function (resolve, reject) {
                    me.$http.get(`http://34.69.232.192/api/v1/namespaces/default/pods/ide-for-${userName}-${userGroup}-${projectName}`).then(function (result) {
                        if (result.data.status.phase == "Running") {
                            resolve(true)
                        } else {
                            resolve(false)
                        }
                    }).catch(function (e) {
                        resolve(false)
                    })
                })
            },
