            deleteFileMinio(userName, userGroup, projectName) {
                var me = this
                return new Promise(function (resolve, reject) {
                    var minioClient = new Minio.Client({
                        endPoint: '35.232.244.51',
                        port: 9000,
                        useSSL: false,
                        accessKey: 'minio',
                        secretKey: 'minio123'
                    });

                    minioClient.removeObject('eventstorming', `${userGroup}/${userName}/${projectName}.zip`, function (err) {
                        if (err) {
                            return console.log('Unable to remove object', err)
                        }
                        me.ideUpdateCheck = false
                        me.ideCheckDialog = false
                        window.open(`#/IdeLoadingPage?param=ide-for-${userName}-${userGroup}-${projectName}`, '_blank')
                        setTimeout(function () {
                            me.openProjectIDE()
                        }, 50000)

                    })
                })
            },
            async ideStartValidation() {
                var me = this
                var userName = localStorage.getItem("author").split('@')[0].toLowerCase();
                var userGroup = localStorage.getItem("author").split('@')[1].split('.')[0].toLowerCase();
                var projectName = me.projectName.toLowerCase();

