            makeElement(ele, idx) {
                var me = this;
                var component
                var check = true

                console.log(ele)
                if (ele._type == "org.uengine.modeling.model.Event") {
                    component = me.getComponentByName('domain-event-definition');
                } else if (ele._type == "org.uengine.modeling.model.BoundedContext") {
                    component = me.getComponentByName('bounded-context-definition');
                } else if (ele._type == "org.uengine.modeling.model.Aggregate") {
                    component = me.getComponentByName('aggregate-definition');
                } else if (ele._type == "org.uengine.modeling.model.Command") {
                    component = me.getComponentByName('command-definition');
                } else if (ele._type == "org.uengine.modeling.model.Policy") {
                    component = me.getComponentByName('policy-definition');
                } else if (ele._type == "org.uengine.modeling.model.View") {
                    component = me.getComponentByName('view-definition');
                } else if (ele._type == "org.uengine.modeling.model.External") {
                    component = me.getComponentByName('external-definition');
                } else if (ele._type == "org.uengine.modeling.model.Actor") {
                    component = me.getComponentByName('actor-definition');
                } else if (ele._type == "org.uengine.modeling.model.Relation") {
                    component = me.getComponentByName('class-relation');
                }

                if (ele._type == "org.uengine.modeling.model.Relation") {
                    check = false
                } else {
                    check = true
                }


                if (check) {
                    var element = component.computed.createNew(
                        ele.elementView.id,
                        ele.elementView.x,
                        ele.elementView.y,
                        ele.elementView.width,
                        ele.elementView.height
                    )

                    // element.name = "tmp";
                    // element.loadData = true;
                    if (component.name == 'bounded-context-definition') {
                        element.aggregates = ele.aggregates
                        element.policies = ele.policies
                    } else if (component.name == 'aggregate-definition') {
                        element.aggregateRoot = ele.aggregateRoot
                        element.boundedContext = ele.boundedContext
                        if (ele.events) {
                            element.events = ele.events
                        } else {
                            element.events = []
                        }
                        if (ele.commands) {
                            element.commands = ele.commands
                        } else {
                            element.commands = []
                        }
                        if (ele.viewes) {
                            element.viewes = ele.viewes
                        } else {
                            element.viewes = []
                        }
                    } else if (component.name == 'domain-event-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = pluralize(ele.aggregate.name);
                        }
                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        // element.eventToPolicy = ele.eventToPolicy

                        element.relationPolicyInfo = ele.relationPolicyInfo
                        element.relationCommandInfo = ele.relationCommandInfo

                        element.fieldDescriptors = ele.fieldDescriptors
                        element.trigger = ele.trigger
                    } else if (component.name == 'command-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        }

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.restfulType = ele.restfulType
                    } else if (component.name == 'policy-definition') {
                        // if (ele.aggregate) {
                        //     ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        //     ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        //     ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        // }

                        // element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        // element.eventToPolicy = ele.eventToPolicy
                        element.relationEventInfo = ele.relationEventInfo
                    } else if (component.name == 'view-definition') {
                        if (Object.keys(ele.aggregate).length !== 0) {
                            element.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            element.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            element.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));
                        }

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.dataProjection = ele.dataProjection
                        element.aggregateEvents = ele.aggregateEvents

                        if (ele.dataProjection == 'cqrs') {
                            console.log(ele)
                            element.viewFieldDescriptors = ele.viewFieldDescriptors
                        } else {
                            // element.fieldDescriptorsMash = ele.viewFieldDescriptors
                        }
                    }

                    // element.portGenerated = ele.portGenerated
                    if (idx != null) {
                        // me.isUndoRedo = true
                        me.value['definition'].splice(idx, 1, element)
                        // me.value['definition'][idx].replace(me.value['definition'][idx],element)
                    } else {
                        me.value['definition'].push(element);
                    }

                } else {
                    var element = component.computed.createNew(
                        ele.relationView.id,
                        ele.sourceElement,
                        ele.targetElement,
                        ele.relationView.value,
                    )

                    if (component.name == 'class-relation') {
                        element.name = ele.name
                        element.from = ele.from
                        element.to = ele.to
                        element.relationView = ele.relationView
                    }

                    if (idx != null) {
                        me.value.relation.splice(idx, 1, element)
                    } else {
                        me.value.relation.push(element);
                    }
                }

                // element.drawer = true;

                me.$nextTick(function () {
                    element.name = ele.name;
                    // element.drawer = false;
                })

                return element
            }
