            loadAddElement: function (value) {
                var me = this;

                // // console.log(JSON.parse(JSON.stringify(value)))
                me.value.definition = [];
                me.value.relation = [];
                me.projectName = value.projectName
                var boundedComponent = me.getComponentByName('bounded-context-definition');
                var aggregateComponent = me.getComponentByName('aggregate-definition');
                var commandComponent = me.getComponentByName('command-definition');
                var eventComponent = me.getComponentByName('domain-event-definition');
                var policyComponent = me.getComponentByName('policy-definition');
                var viewComponent = me.getComponentByName('view-definition');
                var relationComponent = me.getComponentByName('class-relation');

                if (value.boundedContexts)
                    value.boundedContexts.forEach(function (bounded, boundedIdx) {
                        // // console.log(bounded)
                        if (value.boundedContexts[boundedIdx].elementView.id == bounded.elementView.id) {
                            var ele = me.makeElement(bounded)
                            value.boundedContexts[boundedIdx] = ele
                            bounded.aggregates.forEach(function (agg, aggIdx) {
                                // // console.log(agg)

                                if (bounded.aggregates[aggIdx].elementView.id == agg.elementView.id) {
                                    var aggEle = me.makeElement(agg)
                                    bounded.aggregates[aggIdx] = aggEle
                                }
                                if (agg.events) {
                                    agg.events.forEach(function (ev, idx) {
                                        if (agg.events[idx].elementView.id == ev.elementView.id) {
                                            var ele = me.makeElement(ev)
                                            agg.events[idx] = ele
                                        }
                                    })
                                }
                                if (agg.commands) {
                                    agg.commands.forEach(function (co, idx) {
                                        if (agg.commands[idx].elementView.id == co.elementView.id) {
                                            var ele = me.makeElement(co)
                                            agg.commands[idx] = ele
                                        }
                                    })
                                }

                                if (agg.viewes) {
                                    agg.viewes.forEach(function (vi, idx) {
                                        if (agg.viewes[idx].elementView.id == vi.elementView.id) {
                                            var ele = me.makeElement(vi)
                                            agg.viewes[idx] = ele
                                        }
                                    })
                                }
                            })
                            if (bounded.policies) {
                                bounded.policies.forEach(function (po, idx) {
                                    if (bounded.policies[idx].elementView.id == po.elementView.id) {
                                        var ele = me.makeElement(po)
                                        bounded.policies[idx] = ele
                                    }

                                })
                            }
                        }
                    })

                if (value.relations)
                    value.relations.forEach(function (line) {
                        me.makeElement(line)
                    })

            }
            ,
            addElement: function (componentInfo, bounded) {
                this.enableHistoryAdd = true;
                var me = this;
                var additionalData = {};
                var vueComponent = me.getComponentByName(componentInfo.component);
                var element;
                // console.log(bounded)
                if (componentInfo.component == 'class-relation') {
                    /* make Relation */

                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.sourceElement.value,
                        componentInfo.targetElement.value,
                        componentInfo.vertices,
                    );

                    if (componentInfo.targetElement.value._type == 'org.uengine.modeling.model.Policy') {
                        element.name = 'Pub/Sub'

                    } else if (componentInfo.targetElement.value._type == 'org.uengine.modeling.model.Command') {
                        element.name = 'Req/Res'

                    }

                    if ((element.targetElement._type == 'org.uengine.modeling.model.Policy'
                        || element.targetElement._type == 'org.uengine.modeling.model.Command')) {
                        if (bounded != undefined) {
                            if (bounded) {
                                console.log("bounded")
                                return element
                            }
                        } else {
                            me.onAddElementPush(element)

                        }
                    }

                } else {
                    /* make Element */
                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.x,
                        componentInfo.y,
                        componentInfo.width,
                        componentInfo.height
                    );

                    if (bounded != undefined) {
                        if (bounded) {
                            console.log("bounded")
                            return element
                        }
                    } else {
                        me.onAddElementPush(element)

                    }
                }

            }
            ,
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            }
            ,
            getComponentByName: function (name) {
                var componentByName;
                $.each(window.Vue._components, function (i, component) {
                    if (component.name == name) {
                        componentByName = component;
                    }
                });
                return componentByName;
            }
            ,
            getComponentByClassName: function (className) {
                var componentByClassName;

                $.each(window.Vue.classModelingComponents, function (i, component) {
                    if (component.default.computed && component.default.computed.className && component.default.computed.className() == className) {
                        componentByClassName = component.default;
                    }
                });
                return componentByClassName;
            }
            ,
            recursiveDeepCopy(o) {
                var me = this
                var newO,
                    i;

                if (typeof o !== 'object') {
                    return o;
                }
                if (!o) {
                    return o;
                }

                if ('[object Array]' === Object.prototype.toString.apply(o)) {
                    newO = [];
                    for (i = 0; i < o.length; i += 1) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                    return newO;
                }

                newO = {};
                for (i in o) {
                    if (o.hasOwnProperty(i)) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                }
                return newO;
            }
            ,
            getBoundedSetting(boundedId) {
                //deploy
                var me = this
                return new Promise(async function (resolve, reject) {
                    let tmpList = []
                    var fullPathList = []
                    var getCodeList = []
                    var treeList = []
                    me.model = {
                        _type: "org.uengine.modeling.model.Model",
                        boundedContexts: [],
                        projectName: me.projectName
                    }

                    var copyValue = JSON.parse(JSON.stringify(me.value))

                    //boundedContexts 
                    copyValue.definition.forEach(function (item) {
                        if (item != null) {
                            if (item.elementView.id == boundedId) {
                                me.model.boundedContexts.push(item)
                            }
                        }
                    })

                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: [],
                        View: [],
                        RelationEventInfo: [],
                        RelationCommandInfo: [],
                        ViewEventInfo: [],
                    };
                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);
                        bound.groups.forEach(function (groupsId) {
                            if (me.$refs[groupsId][0].value._type == 'org.uengine.modeling.model.Event') {
                                if (me.$refs[groupsId][0].value.aggregate.name == undefined) {
                                    var tempEvent = me.$refs[groupsId][0].value
                                    modelForElements['Event'].push(tempEvent)
                                }

                            }
                        })
                        if (me.model.boundedContexts[idx].aggregates.length >= 1) {
                            me.model.boundedContexts[idx].aggregates.forEach(function (agg, aggIdx) {
                                var lifeCycles = [
                                    {trigger: 'PostPersist', annotation: '@PostPersist', events: []},
                                    {trigger: 'PostUpdate', annotation: '@PostUpdate', events: []},
                                    {trigger: 'PostRemove', annotation: '@PostRemove', events: []},
                                    {trigger: 'PrePersist', annotation: '@PrePersist', events: []},
                                    {trigger: 'PreUpdate', annotation: '@PreUpdate', events: []},
                                    {trigger: 'PreRemove', annotation: '@PreRemove', events: []},
                                ]
                                // fieldDescriptors 
                                agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                    fd.name = fd.name
                                    fd.nameCamelCase = _.camelCase(fd.name)
                                    fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                    if (fd.isKey)
                                        agg.aggregateRoot.keyFieldDescriptor = fd;
                                })
                                modelForElements["Aggregate"].push(agg);
                                modelForElements["AggregateRoot"].push(agg.aggregateRoot);
                                if (agg.commands)
                                    agg.commands.forEach(function (co) {
                                        if (co.name == '') {
                                            me.text = "Check Command Name"
                                            me.snackbar = true
                                            me.color = "error"
                                            me.timeout = 1000
                                            me.checkName = false
                                        }
                                        if (co.controllerInfo.apiPath != '') {
                                            // co.controllerInfo.apiPath =co.controllerInfo.apiPath.replace('/',`//`)
                                        }
                                        modelForElements["Command"].push(co);
                                    })

                                if (agg.events)
                                    agg.events.forEach(function (ev) {
                                        lifeCycles.some(function (lifeCycle) {
                                            if (lifeCycle.annotation == ev.trigger) {
                                                lifeCycle.events.push(ev)
                                                return;
                                            }
                                        })
                                        if (ev.name == '') {
                                            me.text = "Check Event Name"
                                            me.snackbar = true
                                            me.timeout = 1000
                                            me.color = "error"
                                            me.checkName = false
                                        }
                                        modelForElements["Event"].push(ev);

                                        if (ev.relationCommandInfo) {

                                            ev.relationCommandInfo.forEach(function (rci) {
                                                if (rci.commandValue) {
                                                    rci.commandValue.boundedContext = ev.boundedContext;

                                                    rci.commandValue.elementView = {}
                                                    rci.commandValue.elementView.id = ev.elementView.id

                                                    modelForElements["RelationCommandInfo"].push(rci);
                                                }
                                            });

                                        }
                                    })

                                lifeCycles.forEach(function (lifeCycleNull, idx) {
                                    if (lifeCycleNull.events.length == 0) {
                                        lifeCycles[idx] = null;
                                    }
                                })

                                lifeCycles = lifeCycles.filter(n => n);

                                if (lifeCycles.length > 0) {
                                    console.log(lifeCycles)
                                    me.model.boundedContexts[idx].aggregates[aggIdx].lifeCycles = lifeCycles;
                                }
                            })
                        }

                        if (me.model.boundedContexts[idx].policies.length >= 1) {
                            me.model.boundedContexts[idx].policies.forEach(function (po) {
                                if (po.name == '') {
                                    me.text = "Check Policy Name"
                                    me.snackbar = true
                                    me.timeout = 1000
                                    me.color = "error"

                                    me.checkName = false
                                }
                                // console.log(po)
                                po.relationEventInfo.forEach(function (item) {
                                    // console.log(item)
                                    item.eventValue.boundedContext = {}
                                    item.eventValue.boundedContext.name = po.boundedContext.name
                                    item.eventValue.elementView = {}
                                    item.eventValue.elementView.id = po.elementView.id
                                    // item.eventValue.boundedContext.name = po.boundedContext.name
                                    modelForElements["RelationEventInfo"].push(item)
                                })
                                modelForElements["Policy"].push(po);
                            })
                        }

                        if (me.model.boundedContexts[idx].viewes.length >= 1) {
                            me.model.boundedContexts[idx].viewes.forEach(function (vi) {
                                if (vi.name == '') {
                                    me.text = "Check View Name"
                                    me.snackbar = true
                                    me.timeout = 1000
                                    me.color = "error"

                                    me.checkName = false
                                }


                                vi.createRules.forEach(function (fd, index) {

                                    if (Object.keys(fd.when).length == 0) {
                                        vi.createRules[index] = null
                                        vi.createRules = vi.createRules.filter(n => n)
                                    } else {
                                        if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                            var temp = JSON.parse(JSON.stringify(fd))
                                            temp.when.boundedContext = vi.boundedContext
                                            modelForElements["ViewEventInfo"].push(temp.when)
                                        }
                                        fd.fieldMapping.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.fieldMapping[idx] = null
                                                fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                            }
                                        })

                                        fd._index = index + 1
                                    }

                                })

                                vi.updateRules.forEach(function (fd, index) {
                                    if (Object.keys(fd.when).length == 0) {
                                        vi.updateRules[index] = null
                                        vi.updateRules = vi.updateRules.filter(n => n)
                                    } else {
                                        if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                            var temp = JSON.parse(JSON.stringify(fd))
                                            temp.when.boundedContext = vi.boundedContext
                                            modelForElements["ViewEventInfo"].push(temp.when)
                                        }
                                        fd.fieldMapping.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.fieldMapping[idx] = null
                                                fd.fieldMapping = fd.fieldMapping.filter(n => n)
                                            }
                                        })
                                        fd.where.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.where[idx] = null
                                                fd.where = fd.where.filter(n => n)
                                            }
                                        })
                                        fd._index = index + 1
                                    }

                                })
                                vi.deleteRules.forEach(function (fd, index) {
                                    if (Object.keys(fd.when).length == 0) {
                                        vi.deleteRules[index] = null
                                        vi.deleteRules = vi.deleteRules.filter(n => n)
                                    } else {
                                        if (fd.when.boundedContext.id != vi.boundedContext.id) {
                                            var temp = JSON.parse(JSON.stringify(fd))
                                            temp.when.boundedContext = vi.boundedContext
                                            modelForElements["ViewEventInfo"].push(temp.when)
                                        }
                                        fd.where.forEach(function (item, idx) {
                                            if (Object.keys(item.eventField).length == 0 || Object.keys(item.viewField).length == 0) {
                                                fd.where[idx] = null
                                                fd.where = fd.where.filter(n => n)
                                            }
                                        })
                                        fd._index = index + 1
                                    }
                                })

                                modelForElements["View"].push(vi);
                            })
                        }

                    })

                    //template Setting
                    if (me.template.length > 0) {
                        var template = me.template;
                    } else {
                        var template = 'spring-boot';
                    }

                    //   npm   tree       .
                    var lastIndex = me.$manifestsPerTemplate[template].length - 1


                    me.$manifestsPerTemplate[template].forEach(function (element, index) {
                        element = element.replace('./', '')
                        var doit = function (content) {
                            if (element.includes('for-model')) {
                            }
                            var test = ''
                            var count = 0
                            var optionsCheck = content.split('---')
                            if (!content.includes('forEach:')) {
                                count = count + 1
                                if (element.includes('for-model')) {
                                    var forEach = 'Model'
                                    test = test.concat('forEach: ' + forEach)
                                } else {
                                    var forEach = 'BoundedContext'
                                    test = test.concat('forEach: ' + forEach)
                                }
                            } else {
                                var forEach = content.split("forEach:")[1].split("\n")[0].trim();
                            }

                            if (!content.includes('fileName:')) {
                                count = count + 1
                                if (element.split('/')) {
                                    var fileName = element.substring(element.lastIndexOf("/") + 1);
                                    test = test.concat('\n')
                                    test = test.concat('fileName: ' + fileName)
                                }
                            }
                            if (!content.includes('path:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                count = count + 1
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                    test = test.concat(`path: ${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                }
                            } else if (!content.includes('forEach:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                } else if (content.includes(' path:')) {
                                    count = count + 1
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                }
                            }
                            if (count == 3) {
                                test = test.concat('\n---\n')
                            } else {
                                test = test.concat('\n')
                            }

                            if (test.length > 0) {
                                content = test.concat(content)
                            }
                            var model = modelForElements[forEach];

                            // if(model[0]._type == 'org.uengine.modeling.model.Policy' && model[0].relationEventInfo.length > 0)

                            var process = function (ele) {
                                if (ele == undefined) {
                                    var optionsMetadataAndSource = content.split("---"); //  
                                    var optionsPathHeader = yamlpaser.load(optionsMetadataAndSource[0])

                                    var elementPath = element.split('/');
                                    var elementPathString = elementPath[0] + '/' + elementPath[1];
                                    if (!optionsPathHeader['path'].includes(elementPathString)) {
                                        optionsPathHeader['path'] = elementPathString.concat(optionsPathHeader['path'])
                                    }

                                    var optionsPath = optionsPathHeader['path'].split('/');
                                    var optionsPathString = '';
                                    for (var o = 1; o < optionsPath.length; o++) {
                                        if (o == 1) {
                                            optionsPathString = optionsPathString.concat(optionsPath[o]);
                                        } else {
                                            optionsPathString = optionsPathString.concat('/' + optionsPath[o]);
                                        }
                                    }
                                    //gateway2/src/main/java/com/example/template
                                    //gateway/src/main/java/com/example

                                    // FullPath : for-model/gateway/src/main/java/com/example/template/Application.java
                                    // Path: /com/example
                                    // if()
                                    var options = {
                                        "package": `${optionsPathString}`.replace(elementPath[1] + '/src/main/java/', '').replace("-", "").replace(/\//gi, '.'),
                                        "packagePath": `${optionsPathString}`
                                    }

                                    ele = me.model

                                } else {
                                    var options = {
                                        "package": `${me.projectName.replace(" ", ".").replace("-", "")}`,
                                        "packagePath": `src/main/java/${me.projectName.replace(" ", "/").replace("-", "")}`
                                    }
                                }

                                ele.options = options;

                                var gen = window.$Mustache.render(content, ele);
                                var metadataAndSource = gen.split("---"); //  
                                var header = yamlpaser.load(metadataAndSource[0])
                                var options = {}
                                Object.keys(header).forEach(function (keyValue, idx) {
                                    var key = keyValue
                                    var value = Object.values(header)[idx]
                                    if (key == 'path' && (value.lastIndexOf('.') == value.length - 1)) {
                                        value = value.substring(0, value.lastIndexOf(".") - 1)
                                    }
                                    options[key] = value;
                                })

                                var fileName = options['fileName']
                                var fullPath = options['path'] + '/' + options['fileName']
                                // console.log(fullPath)
                                if (fileName.includes('Application'))

                                    if (elementPathString && !fullPath.includes(elementPathString)) {
                                        fullPath = elementPathString.concat(fullPath)
                                    }
                                // /src/main/java/com/example/Application.java
                                // for-model/gateway2/src/main/java/com/example/template/Application.java
                                var source = ''

                                if (metadataAndSource.length > 1) {
                                    for (var data in metadataAndSource) {

                                        if (data != 0 && data < metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data] + '---\n')
                                        } else if (data == metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data])
                                        }
                                    }
                                }

                                var codeValue = {}
                                var fullValue = {}
                                source = source.replace('\n', '')
                                if (source.length > 1) {
                                    if (ele._type == 'org.uengine.modeling.model.Model') {
                                        // /src/main/java/com/example/Application.java
                                        // gateway2/src/main/java/com/example/template/Application.java

                                        codeValue = {
                                            'key': elementPath[1] + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': elementPath[1] + fileName,
                                            'fullPath': fullPath.replace('for-model/', '')
                                        }
                                    } else {
                                        codeValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fullPath': fullPath
                                        }
                                    }


                                    getCodeList.push(codeValue)
                                    fullPathList.push(fullValue)
                                }
                            }


                            if (Array.isArray(model)) {
                                model.forEach(process);
                            } else {
                                process(model);
                            }

                        } //end of doit


                        var xhttp = new XMLHttpRequest();
                        xhttp.onreadystatechange = function () {

                            if (this.readyState == 4 && this.status == 200) {
                                if (me.template == 'GIT') {
                                    var gitCodeObj = JSON.parse(this.responseText);
                                    var gitCodeTmp = Base64.decode(gitCodeObj.content)
                                    doit(gitCodeTmp)
                                } else {
                                    doit(this.responseText);
                                }
                                if (lastIndex == index) {
                                    var tmp = {
                                        'getCodeList': getCodeList,
                                        'fullPathList': fullPathList
                                    }
                                    resolve(tmp);
                                }
                            }
                        };
                        if (me.template == 'GIT') {
                            xhttp.open("GET", me.gitCodeUrl[element], true);
                            xhttp.send();
                        } else {
                            xhttp.open("GET", "/static/templates/" + template + "/" + element, true);
                            xhttp.send();
                        }


                    });

                })

            }
            ,
        }
    }

</script>

