<template>
    <div>
        <v-speed-dial
                v-if="isAdmin"
                left
                absolute
                bottom
                v-model="fab"
                :direction="'top'"
                :transition="'slide-y-reverse-transition'"
        >
            <template v-slot:activator>
                <v-btn
                        v-model="fab"
                        color="pink"
                        fab
                        dark
                        medium
                        style="margin-bottom: 50px;"
                >
                    <v-icon v-if="fab">mdi-close</v-icon>
                    <v-icon v-else>mdi-cloud-question</v-icon>
                </v-btn>
            </template>
            <v-btn
                    fab
                    dark
                    small
                    color="green"
                    @click="openQuestionDialog"
            >
                <v-icon>mdi-pencil</v-icon>
            </v-btn>
            <v-btn
                    fab
                    dark
                    small
                    color="green"
                    @click="submitUserAnswer()"
            >
                <v-icon>mdi-pencil</v-icon>
            </v-btn>
            <v-btn
                    fab
                    dark
                    small
                    color="red"
                    @click="clearMessages()"
            >
                <v-icon>mdi-delete</v-icon>
            </v-btn>
        </v-speed-dial>
        <v-dialog
                v-model="dialog"
                width="500"
        >
            <v-card>
                <v-card-title
                        class="headline grey lighten-2"
                        primary-title
                >
                    {{labInfo.labName}}
                </v-card-title>

                <v-card-text>
                    <v-radio-group v-model="questionType" :mandatory="false" row
                    >
                        <v-radio
                                v-for="n in questionTypeList"
                                :key="n.value"
                                :label="`${n.label}`"
                                :value="n.value"
                        ></v-radio>
                    </v-radio-group>
                    <div v-if="questionType == 'choice'">
                        <!--  -->
                        <v-textarea
                                outlined
                                name="input-7-4"
                                label=""
                                v-model="question"
                        ></v-textarea>
                        <v-slider
                                min="1"
                                max="5"
                                :thumb-size="24"
                                thumb-label="always"
                                v-model="choice"
                                label=" "
                        >
                        </v-slider>
                        <v-text-field
                                v-for="n in choice"
                                v-model="choiceList[n-1]"
                                :label="`${n}`"
                                outlined
                        ></v-text-field>
                    </div>
                    <div v-if="questionType == 'short'">
                        <!--  -->
                        <v-textarea
                                outlined
                                name="input-7-4"
                                label=""
                                v-model="question"
                        ></v-textarea>
                    </div>
                </v-card-text>

                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="primary"
                            text
                            @click="postQuestion()"
                    >
                         
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>
<script>

    import LabBase from "./LabBase"

    export default {
        name: "Question",
        components: {},
        mixins: [LabBase],
        data: () => ({
            isAdmin: false,
            dialog: false,
            questionTypeList: [{label: '', value: 'short'}, {label: '', value: 'choice'}],
            questionType: '',
            choice: 1,
            question: '',
            choiceList: [],
            fab: false,
        }),
        props: {
            labInfo: Object,
            classInfo: Object
        },
        created() {
            var me = this
            if (window.localStorage.getItem('authorized') == 'admin') {
                me.isAdmin = true
            }
        },
        computed: {},
        watch: {},

        mounted() {

        },

        destroyed() {
        },

        methods: {
            openQuestionDialog() {
                this.dialog = true
            },
            clearMessages() {
                var me = this
                var messagePath = `${this.courseId}/runningClass/${this.classId}/messages/`
                var messageStream = me.$minioClient.listObjects('labs', messagePath);
                messageStream.on('data', function (obj) {
                    me.delete(obj.name)
                })
            },
            submitUserAnswer() {

            },
            postQuestion() {
                console.log(this.questionType)
                console.log(this.question)
                if (this.questionType == 'choice') {
                    // 
                    var questionObj = {
                        questionType: this.questionType,
                        question: this.question,
                        choiceList: this.choiceList
                    }
                } else if (this.questionType == 'short') {
                    // 
                    var questionObj = {
                        questionType: this.questionType,
                        question: this.question,
                    }
                }
                var questionPath = `${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/question/question.json`
                this.putObject(questionPath, questionObj)
                this.dialog = false
            }

        }
    }
    ;
</script>
