<template>

    <v-card
            v-if="projectInfomation ==null ||( projectInfomation['img'] == null || projectInfomation['projectName'] == null)"
            outlined
            class="mx-auto"
            style="width: 400px; height: 300px; justify-content: center"
            align="center"


    >
        <v-skeleton-loader
                ref="skeleton"
                type="card"
                class="mx-auto"
        >
        </v-skeleton-loader>


    </v-card>

    <v-card
            v-else
            outlined
            class="mx-auto"
            max-width="400"
            max-height="400"
    >

        <v-img
                :src="projectInfomation['img']"
                max-width="400"
                max-height="300"
        >

            <v-row justify="end" style="margin-right: 1px">
                <v-chip
                        v-if="checkNewChip"
                        class="ma-2"
                        color="red"
                        text-color="white"
                        style="margin-right: 10px"
                >
                    NEW
                </v-chip>
                <v-chip
                        v-if="projectInfomation['type'].includes('k8s')"
                        class="ma-2"
                        color="blue"
                        text-color="white"
                >
                    Kubernetes
                </v-chip>
                <v-chip
                        v-if="projectInfomation['type'].includes('es')"
                        class="ma-2"
                        color="green"
                        text-color="white"
                >
                    EventStroming
                </v-chip>
                <v-chip
                        v-if="projectInfomation['projectType'] == 'mine' && projectInfomation['permissionCount'] > 0 "
                        class="ma-2"
                        color="orange"
                        text-color="white"
                >
                    shared
                </v-chip>
            </v-row>

        </v-img>

        <v-list-item three-line>
            <v-list-item-content>
                <v-list-item-title class="headline mb-1">
                    {{ projectInfomation['projectName'] }}
                </v-list-item-title>
                <v-list-item-subtitle>Author : {{ projectInfomation['projectAuthorEmail'] }}</v-list-item-subtitle>
                <v-list-item-subtitle>Created Date : {{ projectInfomation['date'] }}</v-list-item-subtitle>
                <v-list-item-subtitle>Comment : {{projectInfomation['comment']}}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>
        <v-card-actions>
            <!--toUrl(author, projectId, projectVersion, type) -->
            <v-btn v-if="!projectInfomation['type'].includes('k8s')"
                   text
                   @click.native="$EventBus.$emit('setProjectName',projectInfomation['projectName'])"
                   :to="toStormingUrl(
                               projectInfomation['projectAuthor'],
                              projectInfomation['projectType'],
                               projectInfomation['projectId'],
                               projectInfomation['projectVersion'],
                               )">
                Edit
            </v-btn>

            <v-btn v-else
                   text @click.native="$EventBus.$emit('setProjectName',projectInfomation['projectName'])"
                   :to="toKubernetesUrl(
                               projectInfomation['projectAuthor'],
                               projectInfomation['projectType'],
                               projectInfomation['projectId'],
                               projectInfomation['projectVersion']
                               )">
                Edit
            </v-btn>

            <v-btn
                    v-if="projectInfomation['projectType'] == 'mine' || projectInfomation['projectType'] == 'kuberMine'"
                    color="red"
                    text
                    @click="$EventBus.$emit('deleteProject',projectInfomation)"
            >
                Delete
            </v-btn>

        </v-card-actions>
    </v-card>

</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "ProjectCard",
        props: {
            projectId: String,
            projectType: String
        },
        data() {
            return {
                projectInfomation: {},
                yesterDayDate: null,
            }
        },
        created() {
            var me = this


            firebase.database()
                .ref('definitions/' + me.projectId + '/shareOptions')
                .once('value', function (snapshot) {

                }).then(function (shareOption) {
                firebase.database()
                    .ref('definitions/' + me.projectId + '/versionLists/' + shareOption.val()['lastVersionName'] + '/versionInfo')
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {

                            var setProejctType = me.projectType
                            var type = shareOption.val()['type'] == undefined ? (me.projectId.includes('kuber-') ? 'k8s' : 'es') : shareOption.val()['type']
                            var permissionCount = 0
                            if (me.projectType != 'every' && me.projectType && shareOption.val()['permissions'] != undefined) {
                                permissionCount = Object.keys(shareOption.val()['permissions']).length
                            }

                            if (type == 'k8s') {
                                if (me.projectType == 'mine') {
                                    setProejctType = 'kuberMine'
                                } else if (me.projectType == 'share') {
                                    setProejctType = 'kuberShare'
                                } else if (me.projectType == 'every') {
                                    setProejctType = 'kuberEvery'
                                }
                            }

                            var convertTimeStamp
                            if (snapshot.val().date.toString().includes('')) {
                                var converDate = snapshot.val().date.slice(0, 12)
                                converDate = converDate.replace('', '-').replace('', '-').replace('', '')
                                converDate = converDate.replace(/ /gi, "");
                                convertTimeStamp = Date.parse(converDate)
                                // firebase.database()
                                //     .ref('definitions/' + me.projectId + '/versionLists/' + shareOption.val()['lastVersionName'] + '/versionInfo')
                                //     .update({
                                //         data: convertTimeStamp
                                //     })

                            } else {
                                convertTimeStamp = snapshot.val().date
                            }


                            var obj = {
                                type: type,
                                projectAuthor: shareOption.val()['author'],
                                projectAuthorEmail: shareOption.val()['authorEmail'],
                                saveUser: snapshot.val().saveUser,
                                saveUserEmail: snapshot.val().saveUserEmail,
                                saveUserName: snapshot.val().saveUserName,
                                projectId: me.projectId,
                                projectVersion: shareOption.val()['lastVersionName'],

                                projectType: setProejctType,
                                date: me.convertTimeStampToDate(convertTimeStamp),
                                // date: snapshot.val().date,
                                projectName: snapshot.val().projectName,
                                img: snapshot.val().img,
                                comment: snapshot.val().comment,
                                permissionCount: permissionCount,
                            }




                            var mapProjectobj = {
                                type: obj.type,
                                projectId: obj.projectId,
                                projectName: obj.projectName,
                            }

                            me.$emit("cardProjectName", mapProjectobj)

                            me.projectInfomation = obj
                        }
                    })
            })

        },
        computed: {
            checkNewChip() {
                var me = this
                return me.yesterDayDate < me.projectInfomation.date ? false : false
            },
        },
        methods: {
            convertTimeStampToDate(timeStamp) {
                var date = new Date(timeStamp);
                return date.getFullYear() + " " + (date.getMonth() + 1) + " " + date.getDate() + " " + date.getHours() + " " + date.getMinutes() + ""
            },
            toStormingUrl(author, projectType, projectId, projectVersion) {
                // /storming/:userUid/:projectType/:projectId/:projectVersion/:projectRead/:projectWrite
                return "storming/" + author + "/" + projectType + "/" + projectId + "/" + projectVersion
            },
            toKubernetesUrl(author, projectType, projectId, projectVersion) {
                // /storming/:userUid/:projectType/:projectId/:projectVersion/:projectRead/:projectWrite
                return "kubernetes/" + author + "/" + projectType + "/" + projectId + "/" + projectVersion
            },
        },
    }

</script>
