<template>
    <v-container fluid>

        <v-row>
            <v-col>
                   : {{loginName}} <span v-if="isStudent"></span><span v-else-if="!isStudent"></span>
            </v-col>
            <v-col>
                <v-text-field
                        v-model="loginName"
                        label=""
                        outlined
                ></v-text-field>
            </v-col>
            <v-col>
                <v-btn @click="loginStudent(loginName)"> </v-btn>
                <v-btn @click="loginTeacher(loginName)"> </v-btn>
            </v-col>
        </v-row>

        <!--    -->
        <div v-if="!isStudent">
            <v-row>
                <v-col>
                    <v-text-field
                            v-model="courseName"
                            label=""
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-btn @click="registerCourse(courseName)"></v-btn>

                </v-col>
            </v-row>

            <v-row>
                <v-col>
                    <v-text-field
                            v-model="labName"
                            label=" "
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-btn @click="registerLab(courseName, labName)"> </v-btn>
                </v-col>
            </v-row>



            <v-row>
                <v-col>
                    <v-text-field
                            v-model="clazzName"
                            label=" "
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-btn @click="registerClazz(courseName, clazzName)"> </v-btn>
                </v-col>
            </v-row>

        </div>


        <!--    -->
        <div v-if="isStudent">
            <v-row>
                <v-col>
                    <v-text-field
                            v-model="courseName"
                            label=""
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-text-field
                            v-model="clazzName"
                            label=" "
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-text-field
                            v-model="labName"
                            label=" "
                            outlined
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-btn @click="joinLab(labName)"> </v-btn>
                </v-col>
            </v-row>

            <v-row>
                <v-label>  </v-label>
            </v-row>

            <v-row v-for="(clazz, index) in myClazzList" :key="index">
                {{clazz}}
            </v-row>

        </div>

        <v-row>
            <v-label> </v-label>
        </v-row>

        <v-row v-for="(course, index) in courseList" :key="index">
            {{course}}
        </v-row>

        <v-row>
            <v-label> </v-label>
        </v-row>

        <v-row v-for="(course, index) in labList" :key="index">
            {{course}}
        </v-row>

        <v-row>
            <v-label> </v-label>
        </v-row>

        <v-row v-for="(clazz, index) in clazzList" :key="index">
            {{clazz}}
        </v-row>
    </v-container>

</template>

<script>

    var Minio = require('minio');

    export default {
        name: "labAdminTest",

        components: {},

        data: () => ({
            userName: "sanghoon",
            userGroup: "gmail",
            clazz: 'uengine',


            courseName: 'kubernetes',
            labName: 'lab1',
            clazzName: 'sk1st',
            loginName: 'uengine1@uengine.org',
            isStudent : false,

            minioClient : null,
            courseList: {},
            labList: null,
            clazzList: {},
            myClazzList: {},
            overlay: false
            //
        }),
        computed: {
            loadingStatus() {
                if (Object.keys(this.courseList).length < 1) {
                    return true
                } else {
                    return false
                }
            },
        },
        mounted() {
            var me = this
            var minioClient = new Minio.Client({
                endPoint: "35.232.244.51",
                port: 9000,
                useSSL: false,
                accessKey: "minio",
                secretKey: "minio123"
            });
            me.$minioClient = minioClient
            me.courseLists()
            me.clazzLists()
        },
        methods: {
            /**
             *    
             */
            courseLists(){
                var me = this
                me.labList = new Map()
                var stream = me.$minioClient.listObjectsV2('labs', '', true, '')
                stream.on('data', function (obj) {
                    console.log(obj.name)
                    var tmp = obj.name.split('/');
                    var course = tmp[0];
                    var name = obj.name
                    if( tmp[1] == 'planed' ){
                        var labName = tmp[2]
                        var json = {
                            " " : course,
                            " " : labName
                        }
                        if( me.labList.has(course)){
                            var tmpArray = me.labList.get(course)
                            tmpArray.push(json)
                        }else{
                            me.labList.set(course , [json] )
                        }

                        me.$set(me.courseList, course+'_'+labName, json)
                    }
                })
                stream.on('error', function (err) {
                    console.log(err)
                })
            },
            /**
             *     
             */
            clazzLists(){
                var me = this
                var stream = me.$minioClient.listObjectsV2('labs', '', true, '')
                stream.on('data', function (obj) {
                    console.log(obj.name)
                    var tmp = obj.name.split('/');
                    var course = tmp[0];
                    if( tmp[1] == 'runningClass' ){

                        var clazzName = tmp[2]
                        var json = {
                            "" : course,
                            "" : clazzName,
                            " " : ''
                        }
                        me.$set(me.clazzList, course+'_'+clazzName, json)
                    }
                })
                stream.on('error', function (err) {
                    console.log(err)
                })
            },
            /**
             *     
             */
            myClazzLists(){
                var me = this
                var stream = me.$minioClient.listObjectsV2('labs', '', true, '')
                stream.on('data', function (obj) {
                    console.log(obj.name)
                    var tmp = obj.name.split('/');
                    var course = tmp[0];
                    if( tmp[1] == 'runningClass' ){
                        // TODO
                    }
                })
                stream.on('error', function (err) {
                    console.log(err)
                })
            },
            registerCourse(courseName) {
                var me = this

                me.$minioClient.putObject('labs', `${courseName}/Course_Metadata.json`, '', {
                    'Content-Type': 'application/json',
                } , function (err, etag) {
                    me.courseLists()
                    return console.log(err, etag)
                });
            },
            registerLab(courseName, labName) {
                var me = this
                me.$minioClient.putObject('labs', `${courseName}/planed/${labName}/Lab_Metadata.json`, '', {
                    'Content-Type': 'application/json',
                } , function (err, etag) {
                    me.courseLists()
                    return console.log(err, etag)
                });

            },
            /**
             *   
             */
            registerClazz(courseName, clazzName) {
                var me = this
                me.$minioClient.putObject('labs', `${courseName}/runningClass/${clazzName}/Class_Metadata.json`, '', {
                    'Content-Type': 'application/json',
                } , function (err, etag) {
                    me.clazzLists()
                    return console.log(err, etag)
                });
            },
            /**
             *   
             */
            joinClazz(clazzName) {
                var me = this
                me.$minioClient.putObject('labs', `${me.courseName}/runningClass/${clazzName}/Class_Metadata.json.json`, '', {
                    'Content-Type': 'application/json',
                } , function (err, etag) {
                    return console.log(err, etag)
                });

            },
            /**
             *   
             */
            joinLab(labName) {
                var me = this
                // TODO
                //     /runningClass////   .

                var json = {
                    labPass : false
                }
                me.$minioClient.putObject('labs', `${me.courseName}/runningClass/${me.clazzName}/${labName}/${me.loginName}/User_Lab_Metadata.json`,
                    JSON.stringify(json),
                    {'Content-Type': 'application/json'} ,
                    function (err, etag) {
                        return console.log(err, etag)
                    }
                );

            },
            loginStudent(loginName) {
                this.isStudent = true
                this.myClazzList = {}
                this.myClazzLists()
            },
            loginTeacher(loginName) {
                this.isStudent = false
            }
        }
    }
</script>

