<template>

</template>

<script>
  export default {
    name: 'bpmn-component-finder',
    created: function () {

    },
    data: function () {
      return {
        bpmnComponent: null
      }
    },
    watch: {},
    mounted: function () {
      // bpmn (,,) BpmnVue  .
      var bpmnComponent = null;
      var parent;
      var getParent = function (component) {
        parent = component.$parent;
        if (parent) {
          if (parent.bpmnRole == 'bpmn-component') {
            bpmnComponent = parent;
          } else {
            getParent(parent);
          }
        }
      }
      getParent(this);
      this.bpmnComponent = bpmnComponent;
    }
  }
</script>


