            deleteItem() {
                var me = this;
                var deleteItem, deletePermission;


                if (me.deleteConfirmCheckBox) {
                    if (me.selectDelete.projectType == 'mine' || me.selectDelete.projectType == 'kuberMine') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + "/" + me.selectDelete.projectId)
                    } else if (me.selectDelete.projectType == 'share' || me.selectDelete.projectType == 'kuberShare') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + '/' + me.selectDelete.projectId)
                        deletePermission = firebase.database().ref('/definitions/' + me.selectDelete.projectId + '/shareOptions/permissions/' + localStorage.getItem('uid'))
                    }

                    deleteItem.remove()
                    if (me.selectDelete.projectType == 'share') {
                        deletePermission.remove()
                    }
                    me.deleteConfirmCheckBox = false

                    me.$nextTick(function () {
                        me.getList()
                        me.deleteDialog = false
                    })

                } else {
                    alert("  ''  .")
                }

