                            projectName: localStorage.getItem("KuberLocalSaveProjectName") != null ? localStorage.getItem("KuberLocalSaveProjectName") : 'local',
                            projectType: 'kuberLocal',
                        },
                    ]

                    if (JSON.parse(localStorage.getItem('localLoadData')) != undefined) {
                        // if (Object.keys(JSON.parse(localStorage.getItem('localLoadData'))['elements']).length > 0) {
                        if (JSON.parse(localStorage.getItem('localLoadData')).definition.length > 0) {
                            me.stormingLocalLists = eventStormingLocal
