            async getList() {
                var me = this


                var userId = localStorage.getItem("uid")

                if (window.localStorage.getItem("accessToken") != null) {
                    if (userId) {


                        await me.getLocalList()
                        await me.getMineList()
                        await me.getShareList().then(function () {
                            me.stormingShareKeyLists = []

                            if (me.stormingMineKeyLists.length > 0) {
                                me.stormingShareKeyTempLists.forEach(function (sharedKey) {
                                    if (me.stormingMineKeyLists.findIndex(mineProject => mineProject.projectId == sharedKey.projectId) == -1) {
                                        me.stormingShareKeyLists.push(sharedKey)
                                    }
                                })

                            } else {
                                me.stormingShareKeyLists = me.stormingShareKeyTempLists
                            }

                            me.stormingShareKeyTempLists = []
                            me.$EventBus.$emit('progressValue', false)
                        })
                        await me.getPublicList().then(function () {
                            me.stormingPublicKeyLists = []

                            if (me.stormingMineKeyLists.length > 0) {

                                me.stormingPublicKeyTempLists.forEach(function (everykey) {
                                    if (me.stormingMineKeyLists.findIndex(mineProject => mineProject.projectId == everykey.projectId) == -1) {
                                        me.stormingPublicKeyLists.push(everykey)
                                    }
                                })

                            } else {
                                me.stormingPublicKeyLists = me.stormingPublicKeyTempLists
                            }


                            me.stormingPublicKeyTempLists = []
                            me.$EventBus.$emit('progressValue', false)
                        })


                    } else {
                        await me.getLocalList()
                        await me.getPublicList().then(function () {
                            me.stormingPublicKeyLists = me.stormingPublicKeyTempLists
                            // me.kubernetesPublicKeyLists = me.kubernetesPublicKeyTempLists
                            me.$EventBus.$emit('progressValue', false)
                        })


                    }
                } else {
                    await me.getLocalList()
                    await me.getPublicList().then(function () {
                        me.stormingPublicKeyLists = me.stormingPublicKeyTempLists
                        // me.kubernetesPublicKeyLists = me.kubernetesPublicKeyTempLists
                        me.$EventBus.$emit('progressValue', false)
                    })

                }


            }
        }
