<template>
    <v-container fluid>
        <v-row>
            <v-col align="center">
                <h1>
                {{classInfo.className}}</h1>

                <v-timeline :dense="$vuetify.breakpoint.smAndDown">
                    <v-timeline-item
                        v-for="(lab, index) in labs" :key="index"
                        color="purple lighten-2"
                        fill-dot
                        :left = "index % 2 == 0"
                        :right = "index % 2 == 1"
                    >

                        <v-card style="width: 400px">
                            <!--                    <img v-if="a.imgSrc"-->
                            <!--                         :src="labs.imgSrc"-->
                            <!--                         style="width: 300px; height: 150px"-->
                            <!--                    >-->
                            <v-card-title>
                                <v-chip :color="lab.status=='completed' ? 'green' : 'primary'">{{lab.labId}}</v-chip>
                                {{lab.labName}}
                            </v-card-title>
                            <v-card-text>
                                {{lab.labScenario}} <br>
                                {{lab.labTime}}  
                            </v-card-text>
                            <v-card-actions>

                                <v-spacer></v-spacer>
                                <v-btn :to="$route.fullPath+ '/' +  lab.labId + '/class-room'"></v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-timeline-item>
                </v-timeline>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import LabBase from './LabBase'

    export default {
        name: "ClassDetail",

        components: {},
        mixins: [LabBase],

        data: () => ({
            courseInfo: null,
            classInfo: null,
            labs:[]
        }),

        computed: {
        },
        watch: {},
        mounted() {
            this.load();
        },
        methods: {
            async load() {
                this.courseInfo = await this.getCourseInfo();
                this.classInfo = await this.getClassInfo();

                var me = this;

                for(var i=0; i<this.classInfo.labsList.length; i++){
                    me.labs[i] = (await me.getLabInfo(this.classInfo.labsList[i]))
                    me.labs[i].labId = this.classInfo.labsList[i];
                    me.labs.__ob__.dep.notify();
                }

            },
        }
    };
</script>
