<template>
    <div>
        <geometry-element
                selectable
                :movable="editMode"
                :resizable="editMode"
                connectable
                :deletable=editMode
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:rotateShape="onRotateShape"
                v-on:labelChanged="onLabelChanged"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label.sync="name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >

            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
                        'fill-r': 1,
                        'fill-cx': .1,
                        'fill-cy': .1,
                        'stroke-width': 1.4,
                        'stroke': '#ffeb3b',
                        fill: '#ffeb3b',
                        'fill-opacity': 1,
                        r: '1',
                        'z-index': '998'
                    }"
            ></geometry-rect>

            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="30"
                        :sub-top="0"
                        :sub-left="0"
                        :text="'Deployment'">
                </text-element>
                <image-element
                        :image="imgSrc"
                        :sub-bottom="5"
                        :sub-left="5"
                        :sub-width="24"
                        :sub-height="24"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <property-panel
                v-if="openPanel"
                v-model="value"
                :img="imgSrc">
        </property-panel>
    </div>
</template>

<script>
    import Element from '../Kube-Element'
    import PropertyPanel from './DeploymentPropertyPanel'
    import ImageElement from "../../../opengraph/shape/ImageElement";

    export default {
        mixins: [Element],
        name: 'deployment',
        components: {
            ImageElement,
            "property-panel": PropertyPanel
        },
        props: {},
        computed: {
            defaultStyle() {
                return {}
            },
            className() {
                return 'Deployment'
            },
            imgSrc() {
                return `${window.location.protocol + "//" + window.location.host}/static/image/symbol/kubernetes/deploy.svg`
            },
            createNew(elementId, x, y, width, height) {
                var me = this
                return {
                    _type: this.className(),
                    name: '',
                    namespace: '',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({}),
                        'angle': 0,
                    },
                    outboundVolumes: [],
                    object: {
                        "apiVersion": "apps/v1",
                        "kind": "Deployment",
                        "metadata": {
                            "name": "",
                            "labels": {
                                "app": ""
                            }
                        },
                        "spec": {
                            "selector": {
                                "matchLabels": {
                                    "app": ""
                                }
                            },
                            "replicas": "1",
                            "template": {
                                "metadata": {
                                    "labels": {
                                        "app": ""
                                    }
                                },
                                "spec": {
                                    "containers": [
                                        {
                                            "name": "",
                                            "image": "",
                                            "ports": [
                                                {
                                                    "containerPort": "80"
                                                }
                                            ]
                                        }
                                    ]
                                }
                            }
                        }
                    },
                    connectableType: ["ReplicaSet"]
                }
            },
            namespace: {
                get: function () {
                    return this.value.object.metadata.namespace
                },
                set: function (newVal) {
                    this.value.object.metadata.namespace = newVal
                }
            },
            name() {
                try {
                    return this.value.object.metadata.name;
                } catch (e) {
                    return "";
                }
            },
            outboundVolumeNames() {
                try {

                    var names = "";

                    this.value.outboundVolumes.forEach(element => {
                        names += element.object.metadata.name + ","
                    });

                    return names;

                } catch (e) {
                    return "";
                }
            }
        },
        data: function () {
            return {};
        },
        created: function () {
        },

        mounted() {
            var me = this;
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if (obj.state == "addRelation" && obj.element && obj.element.targetElement
                    && obj.element.targetElement._type == "PersistenceVolumeClaim") {

                    me.value.outboundVolumes.push(obj.element.targetElement);
                }

                if (obj.state == "deleteRelation" && obj.element && obj.element.targetElement
                    && obj.element.targetElement._type == "PersistenceVolumeClaim") {

                    me.value.outboundVolumes.splice(me.value.outboundVolumes.indexOf(obj.element.targetElement), 1);
                    console.log(me.value.outboundVolumes)
                }
            })

        },
        watch: {
            name(appName) {
                this.value.object.metadata.labels.app = appName;
                this.value.object.spec.selector.matchLabels.app = appName;
                this.value.object.spec.template.metadata.labels.app = appName;
                this.value.object.spec.template.spec.containers[0].name = appName;
            },
            outboundVolumeNames(names) {

                console.log(names)
                this.value.object.spec.volumes = [];
                var me = this;
                var i = 0;
                this.value.outboundVolumes.forEach(element => {
                        me.value.object.spec.volumes.push(
                            {
                                "name": "volume" + (++i),
                                "persistenceVolumeClaim": {
                                    "claimName": element.object.metadata.name
                                }
                            }
                        );
                    }
                );
            },

        },

        methods: {}
    }
</script>

