<template>
    <div>
        <geometry-element
                :selectable="movingElement"
                :movable="!isRead && movingElement"
                :deletable="!isRead && movingElement"
                :resizable="!isRead && movingElement"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"

                :customMoveActionExist="isCustomMoveActionExist"
                v-on:customMoveAction="delayedMove"
                :image.sync="setImg"
                :label.sync="namePanel"
                :_style="{
                'label-angle':value.elementView.angle,
               'text-anchor': 'center',
               'vertical-align': 'top',

                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->

            <geometry-rect
                    v-if="movingElement"
                    :_style="{

          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>
            <geometry-rect
                    v-else
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': .5,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <circle-element
                        :sub-align="'center'"
                        :sub-height="value.elementView.height*0.2"
                        :sub-width="value.elementView.height*0.2"
                        :sub-bottom="value.elementView.height/1.55"
                >
                </circle-element>
                <geometry-line
                        :from="[50,35.5]"
                        :to="[50,63]"
                ></geometry-line>
                <geometry-line
                        :from="[28,42]"
                        :to="[72,42]"
                ></geometry-line>
                <geometry-line
                        :from="[50,63]"
                        :to="[72,80]"
                ></geometry-line>
                <geometry-line
                        :from="[50,63]"
                        :to="[27,80]"
                ></geometry-line>
                <!--title-->
                <!--                <text-element-->
                <!--                        :sub-width="'100%'"-->
                <!--                        :sub-height="titleH"-->
                <!--                        :sub-top="0"-->
                <!--                        :sub-left="0"-->
                <!--                        :text="namePanel">-->
                <!--                </text-element>-->

            </sub-elements>
        </geometry-element>
        <modeling-property-panel
                v-if="openPanel"
                v-model="value"
                titleName="Actor"
                :errorMsg="errorMsg"
                @changed="onPropertyUpdated"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'
    import RectangleElement from "../../../opengraph/shape/RectangleElement";
    import CircleElement from "../../../opengraph/shape/CircleElement";
    import EdgeElement from "../../../opengraph/shape/EdgeElement";
    import EllipseElement from "../../../opengraph/shape/EllipseElement";
    import GeometryLine from "../../../opengraph/geometry/Line";
    import SubElements from "../../../opengraph/shape/SubElements";

    export default {
        components: {
            SubElements,
            GeometryLine,
            EllipseElement,
            EdgeElement,
            CircleElement,
            RectangleElement,
        },
        mixins: [Element],
        name: 'actor-definition',
        props: {},
        computed: {
            errorMsg() {
                var me = this
                if (me.namePanel == '') {
                    return 'Must has a name.'
                } else {
                    return ''
                }
            },
            defaultStyle() {
                return {}
            },
            type() {
                return 'Actor'
            },
            className() {
                return 'org.uengine.modeling.model.Actor'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    innerAggregate: {
                        'event': [],
                        'command': [],
                        'view': [],
                        'policy': [],
                        'external': []
                    },
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 140,
                        'style': JSON.stringify({})
                    },
                    boundedContext: ''
                }

            }
        },
        data: function () {
            return {
                isRead: false,
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                newEditUserImg: [],
                setImg: '',
            };
        },
        created: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')
            this.$nextTick(function () {
                this.namePanel = me.value.name
                me.isRead = designer.isRead

            })
        },
        watch: {
            "value.elementView.height":function () {
                this.refreshImg()
            },
            "movingElement": function () {
                this.refreshImg()
            },
            "newEditUserImg": function (newVal) {
                var me = this
                me.refreshImg()
                // me.$nextTick(function () {
                //     if (newVal.length != 0) {
                //         this.setImg = newVal[newVal.length - 1].picture
                //     } else {
                //         this.setImg = ''
                //     }
                // })
            },
            "value.name": function (newVal, oldVal) {
                this.namePanel = newVal
            },
        },
        mounted: function () {
            var me = this
            var designer = me.getComponent('modeling-designer')

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if (obj.state == 'elementMove') {
                    me.movingElement = true
                    var afterMove = JSON.parse(obj.element)
                    me.value.elementView.x = afterMove.x
                    me.value.elementView.y = afterMove.y
                    me.value.elementView.width = afterMove.width
                    me.value.elementView.height = afterMove.height
                }
            })
        },
        methods: {
            refreshImg() {
                var me = this
                var origin = JSON.parse(JSON.stringify(this.setImg))
                this.setImg = 'test'
                me.$nextTick(function () {
                    if (this.setImg == 'test') {
                        this.setImg = ''
                    } else {
                        this.setImg = 'test'
                    }
                })
            },
            // setNameCase:
            //     function (newVal) {
            //         var me = this
            //         me.value.name = newVal
            //     },
            onPropertyUpdated() {
                var me = this
                console.log('onPropertyUpdated:: ACOTOR')
                this.value.name = me.namePanel
                // var value = this.value;
                // this.value = null;
                // this.value = value;
            },

        }

    }
</script>


