<template>

</template>

<script>
  export default {
    name: 'bpmn-vue-finder',
    created: function () {

    },
    data: function () {
      return {
        bpmnVue: null
      }
    },
    watch: {},
    mounted: function () {
      // bpmn (,,) BpmnVue  .
      var bpmnVue = null;
      var parent;
      var getParent = function (component) {
        parent = component.$parent;
        if (parent) {
          if (parent.bpmnRole == 'bpmn-vue') {
            bpmnVue = parent;
          } else {
            getParent(parent);
          }
        }
      }
      getParent(this);
      this.bpmnVue = bpmnVue;
    }
  }
</script>


