<template>
    <v-container fluid>


        <v-bottom-sheet v-model="missionSheet" persistent v-if="labInfo">
            <template v-slot:activator="{ on, attrs }">
                <v-btn
                color="green"
                dark
                v-bind="attrs"
                v-on="on"
                >
                 
                </v-btn>
            </template>
 
            <v-sheet class="text-center" height="200px">
                <v-btn
                class="mt-6"
                text
                color="error"
                @click="missionSheet = !missionSheet"
                >close</v-btn>
                <div class="py-3" v-html="labInfo.instructionHtml"></div>
            </v-sheet>
        </v-bottom-sheet>

        <v-row v-if="classInfo">

            <v-subheader>Class Room Status</v-subheader><br>
        </v-row>
        <v-row v-if="classInfo">

            <v-col align="center" v-for="(group, groupIdx) in classInfo.groupedUsers" :key="groupIdx">
                <v-card width="350px" height="350px">
                    <v-card-title>
                        {{group.groupName}} ({{group.passedMemberCount}}/{{group.users.length}})
                    </v-card-title>
                    <v-card-text center>
                        <v-row align="center">
                            <v-col v-for="(user,userIdx) in group.users">


                                {{userMessages[user.email] ? userMessages[user.email]: ''}}
                                
                                <br>

                                <v-icon x-large @click="selectUser(user)">
                                    mdi-account-circle
                                </v-icon>
                                <br>

                                <font v-if="userResults[user.email] && userResults[user.email].allPassed" color="green">

                                    <b>{{user.name}} <br> {{userResults[user.email] ? userResults[user.email].passMessage: '...'}}</b>
                                </font>
                                <div v-else>
                                    {{user.name}} <br> {{userResults[user.email] ? userResults[user.email].passMessage: '...'}}
                                </div>
                            </v-col>
                        </v-row>
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col v-if="selectedUser">
                <v-navigation-drawer
                        v-model="selectedUser"
                        clipped
                        app
                        stateless
                        width="400"
                >
                    <div align="right" style="margin-top: 5px; margin-right: 5px; margin-bottom: 5px">
                        <v-icon text @click="unselectUser()">mdi-close</v-icon>
                    </div>
                    <v-divider></v-divider>
                    <v-icon x-large
                            v-bind:style="{ color: selectedUser.color }">
                        mdi-account-circle
                    </v-icon>
                    {{selectedUser.name}} - {{selectedUser.email}}
                    <v-list dense>
                        <v-subheader>
                            Checkpoints
                        </v-subheader>
                        <v-list-item v-for="(item,idx) in userResults[selectedUser.email]" :key="idx">
                            <v-list-item-content>
                                <v-list-item-title>{{idx + 1}}. {{item.text}}</v-list-item-title>
                            </v-list-item-content>
                            <v-list-item-action>
                                <v-checkbox
                                        v-model="item.status"
                                        success
                                        readonly
                                ></v-checkbox>
                            </v-list-item-action>
                        </v-list-item>
                    </v-list>
                    <v-divider></v-divider>
                    <v-list v-if="log">
                        <v-subheader>
                            Logs
                        </v-subheader>
                        <log-viewer v-model="log"></log-viewer>
                    </v-list>
                    <v-divider></v-divider>
                    <v-col class="text-center">
                        <answer v-if="selectedUser.email == myId" :lab-info="labInfo" :class-info="classInfo"></answer>
                    </v-col>
                    <v-col class="text-center" cols="12" sm="4">
                        <v-btn :to="'/courses/' + courseId + '/' + classId + '/' + labId + '/' + selectedUser.email"
                               large color="primary"> 
                        </v-btn>
                    </v-col>
                </v-navigation-drawer>
            </v-col>
        </v-row>
        <v-row v-else>
            <v-col>
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
            </v-col>
        </v-row>
        <question v-if="isAdmin" :lab-info="labInfo" :class-info="classInfo"></question>



 
    </v-container>

</template>

<script>

    import LabBase from "./LabBase"
    import Answer from "./Answer"
    import LogViewer from './LogViewer'
    import UserNavigator from "./UserNavigator";
    import Question from "./Question";

    export default {
        name: "ClassRoom",
        components: {
            Question,
            LogViewer,
            UserNavigator,
            Answer,
            Question
        },
        mixins: [LabBase],
        data: () => ({
            classInfo: null,
            userResults: {},
            userMessages: {},
            labInfo: null,
            pollingJob: null,
            isAdmin: false,
            selectedUser: null,
            log: 'not loaded yet',
            show: true,
            missionSheet: false
        }),

        created() {
            var me = this
            if (window.localStorage.getItem('authorized') == 'admin') {
                me.isAdmin = true
            }
        },
        computed: {
        },
        watch: {
        },
        mounted() {
            var me = this

            this.loadLabInfo();
            this.loadClassInfo();

            me.pollingJob = setInterval(function () {
                me.loadAllUserResults()
                me.loadLog()
                me.loadAllUserMessages()
                // if(me.isAdmin) {
                //     me.checkScore();
                // }
            }, 5000)
        },
        destroyed() {
            var me = this
            clearInterval(me.pollingJob)
        },
        methods: {
            async loadAllUserResults() {
                var me = this
                this.classInfo.groupedUsers.forEach(function (group, groupIdx) {
                    group.users.forEach(function (user, userIdx) {
                        me.loadUserResult(user.email)
                        
//                        me.loadMessage(user.email)
                    })
                })

                me.userResults.__ob__.dep.notify()

            },

            async selectUser(user) {
                this.selectedUser = user;
                this.log = null;
            },
            checkScore() {

            },
            unselectUser() {
                this.selectedUser = null;
            },

            async loadLabInfo() {
                this.labInfo = await this.getLabInfo();
            },
            async loadLog() {
                var me = this

                if(me.selectedUser)
                try {
                    var log = await me.getString(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.selectedUser.email}/result.log`)
                    this.log = log
                } catch (e) {
                    console.log(e)
                }
            },
            async loadAllUserMessages() {
                var me = this;
                var messageFiles = await me.list(`${me.courseId}/runningClass/${me.classId}/messages/`)

                me.userMessages = {}; //clear all before get it
                me.classInfo.groupedUsers.forEach(group =>{
                    group.users.forEach(user => {
                        messageFiles.forEach(messageFile => {
                            if(messageFile.endsWith(user.email + '.txt')){
                                var splitted = messageFile.split(".")[0].split("/")
                                me.userMessages[user.email] = splitted[splitted.length-1]
                            }
                        })
                    })
                })
                me.userMessages.__ob__.dep.notify()
            },

            async loadClassInfo() {
                this.classInfo = await this.getClassInfo();
                this.classInfo.__ob__.dep.notify();
            },

            async loadUserResult(userId) {
                var me = this
                try {
                    var userResult = await this.getObject(
                        `${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${userId}/checkpointResults.json`
                    );

                    this.userResults[userId] = userResult;

                    var passInfo = this.getPassInfo(userId);
                    if (!passInfo)
                        this.userResults[userId].passMessage = "N/A";
                    else {
                        this.userResults[userId].passMessage = passInfo.passed + "/" + passInfo.all;

                        this.userResults[userId].allPassed = (passInfo.passed == passInfo.all);
                    }

                    this.calculateGroupStatus();

                } catch (e) {
                    // if (e.code == "NoSuchKey") {
                    //     //       info  .
                    //     await me.putObject(`${this.courseId}/runningClass/${this.classId}/labs/${this.labId}/${userId}/checkpointResults.json`, me.labInfo.checkPoints)
                    // }
                }
            },

            calculateGroupStatus(){
                this.classInfo.groupedUsers.forEach(group=>{
                    var passedMemberCount = 0;
                    group.users.forEach(user=>{
                        if(user.allPassed)
                            passedMemberCount++;
                    })

                    group.passedMemberCount = passedMemberCount;
                });

                this.classInfo.groupedUsers.__ob__.dep.notify();

            },

            getPassInfo(userId) {
                var checkpoints = this.userResults[userId];
                if (!checkpoints)
                    return null;
                else {
                    var passed = 0;
                    for (var i = 0; i < checkpoints.length; i++) {
                        if (checkpoints[i].status) passed++;
                    }
                    return {
                        passed: passed,
                        all: checkpoints.length
                    };
                }

            },


        }
    };
</script>
