        <!-- <modal ref="modal" name="codeModal" :height='"70%"' :width="'80%'">
            <dashEditYaml :status="'add'" :plainText.sync="plainText" :types="types"></dashEditYaml>
        </modal> -->

        <modal name="codeModal" :height='"auto"' :width="'80%'" scrollable>
            <v-card flat>
                <v-card-title>
                    <v-col :col="8">
