                    ></v-textarea>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="kuberSaveProjectComplete()">Save</v-btn>
                    <v-btn color="red darken-1" text @click="kuberSaveDialogClose()">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="kuberShareDialog" max-width="600">
            <v-card>
                <v-card-title class="headline">share setting</v-card-title>

                <v-card-text>
                    <v-col>

                        <v-checkbox v-model="kuberSharedEveryoneBtn" label='share everyone'></v-checkbox>
                    </v-col>
                    <v-textarea
                            outlined
                            name="input-7-4"
                            label="comment"
                            height="40px"
                            :counter="100"
                            v-model="kuberShareCommnet"
                    ></v-textarea>

                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="applyKuberShare()">Apply</v-btn>
                    <v-btn color="red darken-1" text @click="closeKuberShareDialog() ">Close</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="generateZipDialog" max-width="290">
            <v-card>
                <v-card-title class="headline">Generate Zip Archive</v-card-title>
                <v-card-text>
