                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" text @click="generateZip()">Download</v-btn>
                    <v-btn color="red darken-1" text @click="generateZipDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <modeler-image-generator ref="modeler-image-generator"></modeler-image-generator>

    </div>

</template>

<script>
    import TextReader from "@/components/yaml.vue";
    import {
        saveAs
    } from 'file-saver';
    import * as io from 'socket.io-client'
    import {codemirror} from 'vue-codemirror'
    import yaml from 'js-yaml'
    import json2yaml from 'json2yaml'
    import 'codemirror/lib/codemirror.css'
    import 'codemirror/theme/darcula.css'
    import 'codemirror/mode/yaml/yaml.js'
    import axios from 'axios'

    var fs = require('fs');
    var _ = require('lodash');
    var Minio = require('minio');
    var Base64 = require('js-base64').Base64;
    var yamlpaser = require('js-yaml');
    var FileSaver = require('file-saver');
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    var JSZip = require('jszip')
    var jsondiffpatch = require('jsondiffpatch')
    import firebase from 'firebase'


    export default {
        name: 'kube-modeling-designer',
        components: {
            TextReader,
            saveAs,
            io,
            codemirror
        },
        props: {
            elementTypes: Array
        },
        data() {
            return {
                types: 'deployment',
                plainText: "",
                dashOpen: false,
                //db permmsion

                isMyEdit: false,
                isOtherEdit: false,

                isShareSave: false,
                loadingDialog: true,
                codeViewing: false,

                saveItems: [
                    {title: 'Save to Server'},
                ],
                codeItems: [
                    {title: 'Code Preview'},
                    {title: 'Download Archive'},
                ],
                shareItems: [
                    {title: 'Share'},
                ],
                onlineSaveDialog: false,
                onlineSaveComfirmDialog: false,
                isLoadVersion: false,
                //server
                author: '', //uid
                serverComment: '',
                shareComment: '',
                serverDate: '',
                editing: true,
                files: {
                    version: 'mdi-server',
                    code: 'mdi-code-array',
                    save: 'mdi-content-save',
                    open: 'mdi-book-open',
                    md: 'mdi-markdown',
                    txt: 'mdi-file-document-outline',
                    java: 'mdi-language-java',
                    xml: 'mdi-xml',
                    shell: 'mdi-powershell',
                    docker: 'mdi-docker',
                    png: 'mdi-file-image',
                    json: 'mdi-json',
                    python: 'mdi-language-python',
                    fork: ' mdi-silverware-fork',
                    share: 'mdi-share-variant'
                },
                canvas: null,
                dragPageMovable: false,
                value: {
                    'definitions': [],
                    'relations': []
                },
                projectName: '',
                duplicateprojectName: false,
                generateZipDialog: false,
                //undo Redo
                undoRedoStorage: 100,
                undoRedoArray: [],
                undoRedoIndex: 0,
                undoRedoLastIndex: 0,
                imageBase: 'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/symbol/',
                //Zip
                pathTmp: [],
                gitCodeUrl: {},
                //data structure
                model: [],
                fullPathList: [],
                treeList: [],
                getCodeList: [],
                openCode: [],
                template: '',
                templateList: ['Separate File', 'Single File', 'Separate File for kind', 'Helm'],
                treeOpen: true,
                gitUrl: '',
                gitPath: [],
                gitCode: [],
                checkName: true,
                // Children
                drawer: false,
                chatUid: '',
                messageRef: {},
                //version
                versionList: [],
                version: '',
                revisionInfo: {},
                showVersionsDialog: false,
                kubernetesToken: '',
                clusterAddress: '',
                tokenDialog: false,
                projectAuthor: '',
                parmVersion: '',
                parmType: '',
                parmProjectId: '',
                parmUid: '',

                //save projectId
                newProjectId: '',
                //edit
                isRead: false,
                //fork
                forkDialog: false,
                isUndoRedo: false,
                connetServer: '',
                roomId: "masez",
                // img: null,
                rtcLogin: false,
                webrtcDialog: false,

                //multi Edit
                // isYourValueChange: true,
                database: null,

                isSelfEdit: true,
                isLoad: false,
                sharedCount: 0,
                //kuber
                kuberSaveDialogOpen: false,
                kuberSaveProjectComment: '',
                completedQueue: false,
                completedValue: false,
                //share
                kuberShareDialog: false,
                kuberShareCommnet: '',
                kuberSharedEveryoneBtn: false,


            }
        },
        beforeDestroy: function () {
            var me = this

            //rtc
            // me.onLeave()

            //recevie off
            me.database.ref('value').off()


            me.$refs['modeler-image-generator'].save(me.id, me.canvas).then(function (resolve) {

                if (me.parmType == 'kuberLocal') {
                    localStorage.setItem('kuberLocalSaveImg', resolve)
                    localStorage.setItem('kuberLocalSaveProjectName', me.projectName)
                } else if (me.parmType == 'kuberMine' || me.parmType == 'kuberShare' || me.parmType == 'kuberEvery') {

                    if (me.completedQueue || me.completedValue) {

                        me.database.ref('definitions/' + me.parmProjectId + "/versionLists/" + me.parmVersion + "/versionInfo/")
                            .update({
                                projectName: me.projectName,
                                img: resolve
                            });
                    }


                }
                me.$EventBus.$emit('updatePic', resolve)

            })

            localStorage.removeItem('kuberProjectId')

        },
        computed: {
            checkValueRelation() {
                return _.cloneDeep(this.value)
            },
            shareIconTypes() {
                var me = this
                if (me.isMultiShareType == 'ReadOnly') {
                    return 'mdi-eye'
                } else {
                    return 'mdi-pencil'
                }

            },
            id: {
                get: function () {
                    return this.projectName
                }
            },
        },
        created: function () {
            var me = this
            me.database = firebase.database()

            //database db setting
            window.io = io

            me.parmUid = this.$route.params.userUid;
            me.parmType = this.$route.params.projectType;
            me.parmProjectId = this.$route.params.projectId;
            me.parmVersion = this.$route.params.projectVersion;

            me.roomId = 'msaez_kuber_' + me.parmProjectId


            var valueInit = {"definitions": [], "relations": []}

            var userUid = localStorage.getItem('uid')


            if (me.parmType == 'kuberLocal') {
                localStorage.setItem('kuberProjectId', me.parmProjectId)//kuberInit
                me.projectName = localStorage.getItem('kuberLocalSaveProjectName') != null ? localStorage.getItem('kuberLocalSaveProjectName') : 'kuberLocal'

                me.isRead = false
                if (localStorage.getItem('kuberLocalLoadData') != null) {
                    me.value = JSON.parse(localStorage.getItem('kuberLocalLoadData'));
                } else {
                    me.value = valueInit
                }
            } else {
                me.setKuberServerValueSetting()
            }

        },
        mounted: function () {
            var me = this

            me.$EventBus.$on('login', function (newVal) {
                if (newVal != null) {
                    me.parmUid = localStorage.getItem('uid')
                }
            })

            //
            me.$EventBus.$on('kuberStorage', function (newVal) {
                var saveDate = JSON.parse(JSON.stringify(me.value))
                saveDate.definitions = saveDate.definitions.filter(n => n)
                saveDate.relations = saveDate.relations.filter(n => n)

                if (me.parmType == 'kuberLocal') {
                    // 
                    localStorage.removeItem('kuberLocalLoadData')
                    localStorage.removeItem('kuberLocalSaveTime')
                    localStorage.removeItem('kuberLocalSaveProjectName')

                    //local save
                    localStorage.setItem('kuberLocalLoadData', JSON.stringify(saveDate))
                    localStorage.setItem('kuberLocalSaveTime', me.getNowTime())
                    localStorage.setItem('kuberLocalSaveProjectName', me.projectName)
                }

            });

            //value setting
            me.onKuberRecevieServerValue()

            this.$nextTick(function () {

                let startTime = new Date().getTime()

                this.canvas._CONFIG.FAST_LOADING = false;


                this.$refs.opengraph.printTimer(startTime, new Date().getTime());

                $(document).keydown((evt) => {

                    if (evt.keyCode == 67 && (evt.metaKey || evt.ctrlKey)) {
                        // me.copy();
                    } else if (evt.keyCode == 86 && (evt.ctrlKey || evt.metaKey)) {
                        // me.paste();
                    } else if (evt.keyCode == 90 && (evt.metaKey || evt.ctrlKey)) {
                        if (evt.shiftKey) {
                            // me.redo();
                        } else {
                            // me.undo();

                        }
                    }

                });


            });

        },
        watch: {

            checkValueRelation: {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me = this
                    var delta


                    if (me.isLoad == false) {
                        if (me.isUndoRedo == false) {
                            delta = jsondiffpatch.diff(oldVal, newVal);

                            if (delta != undefined) {

                                if (me.parmType != 'kuberLocal') {

                                    //mine [myself or everyone]
                                    if ((me.sharedCount == 0 || me.isPublicProject) && me.parmType == 'kuberMine') {

                                        //setting   .
                                        if (me.completedValue) {
                                            if (me.isOtherEdit == true) {
                                                me.isOtherEdit = false
                                            } else {
                                                me.isMyEdit = true

                                                var saveDate = JSON.parse(JSON.stringify(newVal))
                                                saveDate.definitions = saveDate.definitions.filter(n => n)
                                                saveDate.relations = saveDate.relations.filter(n => n)

                                                var valueRef = me.database.ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion)
                                                valueRef
                                                    .once('value', function (snapshot) {
                                                        if (snapshot.exists()) {
                                                            if (snapshot.val()['versionValue'].queue != undefined) {
                                                                valueRef.child('/versionValue/queue').remove()
                                                            }
                                                        }
                                                    }).then(function () {
                                                    valueRef.child('/versionValue').update({
                                                        value: JSON.stringify(saveDate)
                                                    })
                                                    console.log('save Server')
                                                })
                                            }
                                        }


                                    }

                                    // me.$EventBus.$emit('kuberUndoRedo', delta)
                                } else {
                                    // me.$EventBus.$emit('kuberUndoRedo', delta)
                                    me.$EventBus.$emit('kuberStorage')
                                }


                            }
                        }
                        me.isUndoRedo = false
                    } else {
                        me.isLoad = false
                    }

                }
            },
            "projectName":
                _.debounce(
                    function (newVal) {
                        this.$EventBus.$emit('kuberStorage')
                    }, 200
                ),
            messageRef:

                function (newVal) {
                    // var me = this
                    // newVal.on('child_added', function (value) {
                    //     // console.log(value.val())
                    //     if (value.val().uid != localStorage.getItem('uid')) {
                    //         var element = JSON.parse(value.val().value)
                    //         me.editing = false;
                    //         me.$EventBus.$emit(`${element.elementView.id}`, element)
                    //     }
                    // });
                },
            async template() {
                var me = this
                me.getListSetting();

                me.$nextTick(function () {
                    me.treeOpen = true
                })
            }
        },
        methods: {

            applyKuberShare() {
                var me = this
                console.log(me.kuberSharedEveryoneBtn)
                if (me.kuberSharedEveryoneBtn) {
                    //everyone
                    me.database.ref('userLists/everyone/kuberShare/' + me.parmProjectId)
                        .update({
                            projectId: me.parmProjectId
                        })

                    me.database.ref('definitions/' + me.parmProjectId + '/shareOptions/permissions')
                        .child('everyone')
                        .update({
                            write: false
                        })


                } else {
                    me.database.ref('userLists/everyone/kuberShare/' + me.parmProjectId).remove()

                }

                me.kuberSaveProjectComment = ''
                me.kuberShareDialog = false
            },
            openKuberShareDialog() {
                var me = this

                me.database
                    .ref('definitions/' + me.parmProjectId + '/shareOptions/permissions')
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {
                            if (Object.keys(snapshot.val()) == 'everyone') {
                                me.kuberSharedEveryoneBtn = true
                            } else {
                                me.kuberSharedEveryoneBtn = false

                                /*
                                permission   .
                                 */
                            }

                        } else {
                            me.kuberSharedEveryoneBtn = false
                        }
                    })

                me.kuberShareDialog = true
            },
            closeKuberShareDialog() {
                var me = this

                me.kuberShareDialog = false
            },
            async onKuberRecevieServerValue() {
                var me = this

                //permssion setting
                await me.setKuberPermission()

                await me.onKuberReceiveValue().then(function () {
                    console.log('>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>on KuberReceiveValue<<<<<<<<<<<<<<<<<<<<<<<<<<<<')
                    me.$nextTick(function () {
                        me.completedValue = true
                        me.$EventBus.$emit('progressValue', false)
                    })
                })
                console.log('>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>Done onKuberReceiveValue<<<<<<<<<<<<<<<<<<<<<<<<<<<<')


            },
            onKuberReceiveValue() {
                var me = this
                return new Promise(function (resolve, reject) {

                    me.database.ref('definitions/' + me.parmProjectId + '/versionLists/' + me.parmVersion)
                        .on('value', function (snapshot) {

                            if (snapshot.exists()) {

                                if (me.completedValue) {

                                    if (me.isMyEdit == true) {
                                        me.isMyEdit = false
                                    } else {
                                        me.isOtherEdit = true
                                        me.value = JSON.parse(snapshot.val()['versionValue'].value)
                                        me.$nextTick(function () {
                                            me.$EventBus.$emit('progressValue', false)
                                        })
                                    }


                                } else {
                                    me.value = JSON.parse(snapshot.val()['versionValue'].value)
                                }


                                resolve()
                            }

                        })

                })
            },
            async setKuberServerValueSetting() {
                var me = this
                var app = me.getComponent('App')

                // 
