            },
            setKuberPermission() {
                var me = this

                return new Promise(function (resolve) {
                    me.database.ref('definitions/' + me.parmProjectId)
                        .child('/shareOptions')
                        .on('value', function (value) {
                            if (value.exists()) {
                                me.projectAuthor = value.val()['author']
                                me.projectAuthorEmail = value.val()['authorEmail']
                                me.isExistAuthor = value.val()['isExistAuthor']


                                if (value.val()['type'] == undefined || value.val()['type'] == 'es' ) {
                                    me.database.ref('definitions/' + me.parmProjectId + '/shareOptions').update({
                                        type: 'k8s',
                                    })
                                    console.log('set Type:k8s')
                                }


                                if (me.isExistAuthor == undefined) {
                                    //init
                                    if ((me.projectAuthor == localStorage.getItem('uid') && me.projectAuthor == me.parmUid)) {
                                        me.database.ref('definitions/' + me.parmProjectId + '/shareOptions').update({
                                            isExistAuthor: true
                                        })
                                    } else {
                                        if (me.isExistAuthor == undefined) {
                                            me.isExistAuthor = false
                                        }
                                    }
                                }


                                if (me.projectAuthor == localStorage.getItem('uid') && me.projectAuthor == me.parmUid) {
                                    //My
                                    me.isRead = false

                                    if (value.val()['permissions'] == undefined) {
                                        me.sharedCount = 0
                                    } else {
                                        if (Object.keys(value.val()['permissions']).indexOf('everyone') == -1) {
                                            me.sharedCount = Object.keys(value.val().permissions).length
                                        } else {
                                            me.isPublicProject = true
                                        }
                                    }


                                } else {
                                    //other Person
                                    if (value.val()['permissions'] == undefined) {
                                        me.isRead = true
                                        alert('Wrong Way')
                                        me.$router.push('/')

                                    } else {
                                        if (Object.keys(value.val()['permissions']).indexOf('everyone') != -1) {
                                            //everyone
                                            me.isPublicProject = true
                                            me.sharedCount = -2
                                            me.isRead = true

                                        } else {
                                            me.sharedCount = Object.keys(value.val().permissions).length

                                            if ((Object.keys(value.val()['permissions']).indexOf(localStorage.getItem('uid')) != -1)) {

                                                if (value.val()['permissions'][localStorage.getItem('uid')].write == false) {
                                                    me.isRead = true
                                                    me.forkDialog = true
                                                } else {
                                                    me.isRead = false
                                                }
                                            } else {
                                                //not permission
                                                me.isRead = true
                                                me.$router.push('/')
                                                alert('Permission denied')
                                            }


                                        }
                                    }
                                }
                                console.log('Permission setting Complete')
                                me.isSetPermission = true
                                resolve()
                            }
                        })

                })

            },
