                            name: saveDataObj.userName,
                            picture: localStorage.getItem('picture')
                        })


                        me.database.ref('userLists/' + saveDataObj.author + "/mine/" + getProjectId)
                            .update({
                                projectId: getProjectId
                            })


                        if (me.parmType == 'kuberLocal' && saveType == 'New') {
                            localStorage.removeItem('kuberLocalLoadData')
                            localStorage.removeItem('kuberLocalSaveTime')
                            localStorage.removeItem('kuberLocalSaveProjectName')
                        }
                        me.kuberSaveDialogOpen = false

                        me.$router.push({path: '/'});

                    } else if (saveType == 'newVersion') {
                        //Save
                        //now ProjectId
                        //new versionId

                        //mine - new Version
                        //shared - Write  : Save Project: newVersion

                        getProjectId = localStorage.getItem('kuberProjectId') != null ? localStorage.getItem('kuberProjectId') : me.parmProjectId


                        newVersionId = me.database.ref('definitions/').child(getProjectId + '/versionLists/').push().key

                        me.database.ref('definitions/').child(getProjectId + '/versionLists/' + newVersionId + '/versionValue').update({
                            value: saveDataObj.value
                        })

                        me.database.ref('definitions/').child(getProjectId + '/versionLists/' + newVersionId + '/versionInfo').update({
                            saveUser: saveDataObj.author,
                            saveUserEmail: saveDataObj.email,
                            saveUserName: saveDataObj.userName,
                            projectName: setProjectName,
                            img: saveDataObj.img,
                            date: saveDataObj.date,
                            comment: saveDataObj.comment,
                        })


                        me.database.ref('definitions/').child(getProjectId + '/shareOptions').update({
                            lastVersionName: newVersionId
                        })

                        me.kuberSaveDialogOpen = false

                    }

                } else {
                    me.$EventBus.$emit('showLoginDialog')
                    alert(" server   (   ) :   .")
                }


            },
