<template>
    <v-container fluid style="height: 100%">
        <v-row no-gutters v-if="labInfo" style="height: 100%">
            <v-navigation-drawer
                    v-model="guideOpened"
                    clipped
                    app
                    width="400"
            >
                <div align="right" style="margin-top: 5px; margin-right: 5px; margin-bottom: 5px">
                    <v-icon text @click="closeGuide()">mdi-close</v-icon>
                </div>

                <v-list dense>
                    <v-subheader>
                        Instruction
                    </v-subheader>

                    <v-col>
                        <v-sheet v-html="labInfo.instructionHtml"></v-sheet>
                    </v-col>


                    <v-divider></v-divider>

                    <v-subheader>
                        Checkpoints
                    </v-subheader>
                    <v-list-item v-for="(item,idx) in labInfo.checkPoints" :key="idx">
                        <v-list-item-content>
                            <v-list-item-title>{{idx + 1}}. {{item.text}}</v-list-item-title>
                        </v-list-item-content>
                        <v-list-item-action>
                            <v-checkbox
                                    v-model="item.status"
                                    success
                                    :readonly="!isAdmin"
                            ></v-checkbox>
                        </v-list-item-action>
                    </v-list-item>
                </v-list>
                <v-divider></v-divider>

                <v-col class="text-center" cols="12" sm="4" v-if="isAdmin || myId==userId">
                    <v-btn @click="submitResult()" large color="primary"></v-btn>
                </v-col>

            </v-navigation-drawer>
            <component
                    :is="'lab-tool-' + labInfo.tool"
                    v-model="tool"
                    :lab-info="labInfo"
                    @change="onResultChanged"
                    style="height: 100% !important; width: 100%">
            </component>
        </v-row>
        <v-row v-else>
            <v-progress-circular
                    :size="50"
                    color="primary"
                    indeterminate
            ></v-progress-circular>
        </v-row>
    </v-container>
</template>

<script>
    var Minio = require('minio');
    import LabBase from "./LabBase"

    import LabToolEventStorming from "./tools/LabToolEventStorming"
    import LabToolIde from "./tools/LabToolIDE"

    export default {
        name: "LabLocator",
        components: {
            LabToolEventStorming,
            LabToolIde
        },
        mixins: [LabBase],
        props: {},
        data: () => ({
            labInfo: null,
            overlay: false,
            tool: {result: null},
            guideOpened: true
        }),
        computed: {
            userId() {
                if (this.$route.params.userId)
                    return this.$route.params.userId;

                return this.myId;
            }
        },
        // watch: {
        //     "tool.result": {
        //         immediate: true,
        //         deep: true,
        //         handler(newVal) {
        //             if (newVal != null) {
        //                 this.testCheckpoints(newVal);
        //             }
        //         }
        //     }

        // },
        beforeDestroy() {
        },
        async mounted() {
            var me = this

            await this.loadLabInfo();
        },
        methods: {

            onResultChanged(result) {
                this.testCheckpoints(result)
            },

            async loadLabInfo() {
                this.labInfo = await this.getLabInfo();

                var me = this;
                var storedResult = await this.getObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/toolResult.json`)
                if(storedResult) //important!
                    this.tool.result = storedResult;
                else{
                    var startingArtifact = await this.getObject(`${me.courseId}/planed/${me.labId}/${me.labInfo.startArtifact}`)
                    if(startingArtifact) //important!
                        this.tool.result = startingArtifact;
                }

                this.tool.__ob__.dep.notify();

            },
            testCheckpoints(result) {
                var me = this
                this.labInfo.checkPoints.forEach(async function (checkpoint, idx) {
                    var result = me.tool.result;
                    var passed = false;
                    if (checkpoint.javascript) {
                        if (checkpoint.javascript.endsWith('.js')) {
                            var jsPath = `${me.$route.params.courseId}/planed/${me.$route.params.labId}/${checkpoint.javascript}`
                            checkpoint.javascript = await me.getString(jsPath);
                        }

                        function returnValue(value) {
                            passed = value
                        }
                        var javascript = checkpoint.javascript;
                        eval(javascript);
                    } else if (checkpoint.regExp) {
                        var expression = new RegExp(checkpoint.regExp);
                        passed = expression.test(me.tool.result);
                    } else if (checkpoint.testShell){
                        var testKey = checkpoint.testShell;

                        passed = me.testCheckpointWithShellInlineTest(result, testKey)

                    }

                    checkpoint.status = passed
                    checkpoint.__ob__.dep.notify()
                })

            },

            testCheckpointWithShellInlineTest(result, testKey){
                const prefix = "####### test ";

                var lines = result.split("\n");
                var answerPart = null;
                var testKeyPart = null;

                for(var i=0; i<lines.length; i++){
                    var line = lines[i];
                    if(line.startsWith(prefix)){

                        var testKeyAndRegExp = line.split(":")
                        testKeyPart = "test " + testKeyAndRegExp[0].substring(prefix.length, testKeyAndRegExp[0].length).trim();
                        answerPart = testKeyAndRegExp[1].trim();
                        continue;
                    }

                    if(testKeyPart==testKey && answerPart && line.includes(answerPart)){
                        return true;
                        answerPart = null;
                    }
                }

                return false;
            },

            closeGuide() {
                // 
                var me = this
                this.guideOpened = false
                this.$EventBus.$emit('guideClose')
                this.$EventBus.$on('guideOpen', function () {
                    me.guideOpened = true
                })
            },

            async submitResult() {
                var me = this;
                await this.putObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/checkpointResults.json`, me.labInfo.checkPoints)
                await this.putObject(`${me.courseId}/runningClass/${me.classId}/labs/${me.labId}/${me.userId}/toolResult.json`, this.tool.result)
                alert('');
            },

        },


    };
</script>
