<template>
    <div style="position:fixed; text-align: center; background-color: #1e1e1e; height: 100%; width: 100%">
        <v-progress-circular
                style="margin-top: 25%; color: #1976d2 !important"
                :size="130"
                color="primary"
                indeterminate
        >
            {{checkTime}} ...
        </v-progress-circular>
    </div>
</template>

<script>
    export default {
        name: "IdeLoadingPage",
        data() {
            return {
                intervalVariable: '',
                timeOutVariable: '',
                checkTime: 0
            }
        },
        mounted() {
            var me = this
            me.intervalVariable = setInterval(() => me.checkStatus(), 5000);
            me.checkTimeVariable = setInterval(() => me.checkTime = me.checkTime + 1, 1000);
        },
        methods: {
            checkStatus() {
                var me = this
                this.$http.get(`http://34.69.232.192/api/v1/namespaces/default/pods/${me.$route.query.param}/status`).then(function (result) {
                    if (result.data.spec.containers[0].readinessProbe.successThreshold > 0) {
                        console.log("done")
                        clearInterval(me.intervalVariable);
                        var redirectUrl = 'http://' + me.$route.query.param + '.msastudy.io/';
                        console.log(redirectUrl);

                        setTimeout(function () {
                            clearInterval(me.checkTimeVariable);
                            window.location.href = redirectUrl;
                        }, 60000);


                        // window.location.href = redirectUrl;
                    }
                }).catch(function (e) {
                })

            }
        }
    }
</script>

