<template>
    <v-container fluid>
        <h1 v-if="teacherClassList.length > 0">  </h1>
        <v-row>
            <v-col v-if="!loaded" align="center" justify="center">
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
            </v-col>
            <v-col v-for="(clazz, index) in teacherClassList" :key="index" md="3">
                <v-card>
                    <!--v-img :src="course.imgSrc"></v-img-->
                    <v-card-title>
                        <!--                        {{course}}-->
                        {{clazz.className}}
                    </v-card-title>
                    <v-card-text>
                         : {{clazz.course ? clazz.course.courseDesc : clazz.courseId}} <br>
                          : {{clazz.classStartDate}} <br>
                          : {{clazz.classEndDate}} <br>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn text
                               :to="'/courses/'+clazz.courseId+'/'+clazz.classId">
                            
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
        <h1>  </h1>
        <v-overlay :value="overlay">
            <v-btn
                    icon
                    @click="overlay = false"
            >
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
                <v-icon>mdi-close</v-icon>
            </v-btn>
        </v-overlay>
        <v-row>
            <v-col v-if="!loaded" align="center" justify="center">
                <v-progress-circular
                        :size="50"
                        color="primary"
                        indeterminate
                ></v-progress-circular>
            </v-col>
            <v-col v-for="(clazz, index) in clazzList" :key="index" md="3">
                <v-card>
                    <!--v-img :src="course.imgSrc"></v-img-->
                    <v-card-title>
                        <!--                        {{course}}-->
                        {{clazz.className}}
                    </v-card-title>
                    <v-card-text>
                         : {{clazz.course ? clazz.course.courseDesc : clazz.courseId}} <br>
                          : {{clazz.classStartDate}} <br>
                          : {{clazz.classEndDate}} <br>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn text
                               :to="'/courses/'+clazz.courseId+'/'+clazz.classId">
                            
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    var Minio = require('minio');

    export default {
        name: "ClazzListPage",
        components: {},
        data: () => ({
            clazzList: [],
            teacherClassList:[],
            overlay: false,

            email: '',
            loaded: false
        }),
        computed: {
            // loadingStatus() {
            //     if (Object.keys(this.clazzList).length < 1) {
            //         return true
            //     } else {
            //         return false
            //     }
            // },
            sortedClazzList() {
                var me = this
                var o = me.clazzList;
                var sorted = {},
                    key, a = [];
                //    
                for (key in o) {
                    if (o.hasOwnProperty(key)) a.push(key);
                }
                //   
                a.sort();
                //     object 
                for (key = 0; key < a.length; key++) {
                    sorted[a[key]] = o[a[key]];
                }
                return sorted;
            }
        },
        watch: {},
        mounted() {
            var me = this
            this.email = localStorage.getItem("email");
            if (this.email) {
                this.getClazzList();

            }


        },
        methods: {

            async getClazzList() {
                var me = this


                await new Promise((resolve, reject) => {

                    this.$minioClient.listObjects('labs', '', true)
                        .on('data', function (obj) {
                            if (obj.name && obj.name.includes('Class_Metadata') && obj.size != 0) {
                                me.$minioClient.getObject('labs', obj.name, function (err, dataStream) {
                                    if (err) {
                                        return console.log(err)
                                    }
                                    dataStream.on('data', function (chunk) {
                                        var string = new TextDecoder("utf-8").decode(chunk);
                                        var classMetadata = JSON.parse(string)
                                        // json["path"] = obj.name.replace('Class_Metadata.json', '')
                                        // var thumbnailURL = obj.name.replace('Class_Metadata.json', 'thumbnail.png')
                                        if (classMetadata.teacherId == me.email) {
                                            // 
                                            me.teacherClassList.push(classMetadata)
                                        }
                                        // filtering
                                        classMetadata.groupedUsers.some(item => {
                                            return item.users.some(user => {
                                                    if (user.email == me.email) {

                                                        // get course info
                                                        var nameParts = obj.name.split("/");
                                                        console.log(nameParts)
                                                        classMetadata.classId = nameParts[2];
                                                        classMetadata.courseId = nameParts[0];

                                                        me.$minioClient.getObject('labs', classMetadata.courseId + "/Course_Metadata.json", function (err, dataStream) {
                                                            if (err) {
                                                                return console.log(err)
                                                            }
                                                            dataStream.on('data', function (chunk) {
                                                                var string = new TextDecoder("utf-8").decode(chunk);
                                                                var courseMetadata = JSON.parse(string)
                                                                classMetadata.course = courseMetadata;

                                                                //notify manually
                                                                classMetadata.__ob__.dep.notify()
                                                            })

                                                            dataStream.on('error', function (err) {
                                                                console.log(err)
                                                            })
                                                        })

                                                        me.clazzList.push(classMetadata)

                                                        return true;

                                                    }
                                                }
                                            )
                                        })


                                        // me.$minioClient.getObject('labs', thumbnailURL, function (err, dataStream) {
                                        //     if (err) {
                                        //         return console.log(err)
                                        //     }
                                        //     dataStream.on('data', function (chunk) {
                                        //         var base64Data = btoa(String.fromCharCode.apply(null, chunk))
                                        //         json['imgSrc'] = 'data:image/png;base64,' + base64Data
                                        //         json['course'] = obj.name.split('/')[0];
                                        //         me.$set(me.clazzList, obj.name.split('/')[0] + '/' + obj.name.split('/')[2], json)
                                        //     })
                                        //     dataStream.on('error', function (err) {
                                        //         console.log(err)
                                        //     })
                                        // })
                                    })
                                    dataStream.on('error', function (err) {
                                        console.log(err)
                                    })
                                })
                            }
                        })
                        .on('error', function (err) {
                            alert(err);
                            reject();
                        })
                        .on('end', function (err) {
                            resolve();
                        })

                });

                me.loaded = true;


            },
        }
    };
</script>
